import { METADATA_MANAGER } from "@/misc/Symbol";
import { inject } from 'vue';

class MetadataManager {
    constructor(request) {
        this.request = request;
        this.loadedMetadataMap = {}
    }

    async get(module, entity) {
        var key = `${module}/${entity}`;
        if (!this.loadedMetadataMap[key]) {
            var response = await this.request.get(`/${module}/${entity}/.frontend-metadata`);
            this.loadedMetadataMap[key] = JSON.parse(response.data.data.metadata);
        }

        return this.loadedMetadataMap[key];
    }
}

/**
 * 注意这个方法只能在setup函数中调用，其他时候调用会返回undefined
 * @returns MetadataManager的实例
 */
export function useMetadataManager() {
    return inject(METADATA_MANAGER);
}

const MetadataManagerInstaller = {
    install(Vue, options) {
        var metadataManager = new MetadataManager(options.request);
        Vue.provide(METADATA_MANAGER, metadataManager);
        Vue.$daisyMetadataManager = metadataManager;

        Object.defineProperties(Vue.config.globalProperties, {
            $daisyMetadataManager: {
                get: function get() {
                    return metadataManager;
                }
            }
        });
    }
};

export default MetadataManagerInstaller;
