import { computed, unref } from 'vue';

function sleep(ms) {
    return new Promise(resolve => setTimeout(resolve, ms));
}

sleep;

function getNaiveLayout(fieldList, dictionaryManager) {
    if (!fieldList) {
        return {}
    }
    var rowList = [];
    var row = { id: 0, columnList: [] };
    var columnCount = 0;
    for (let index in fieldList) {
        let field = fieldList[index];

        if (field.name == 'id') {
            continue;
        }
        if (field.showOnForm == "disable") {
            continue;
        }

        if (columnCount > 24 - 8) {
            columnCount = 0;
            rowList.push(row);
            row = { id: rowList.length, columnList: [] };
        }
        row.columnList.push({
            id: row.columnList.length,
            type: 'text',
            text: (field.title || field.name) + "：",
            align: 'right',
            width: 3,
        });
        columnCount += 3;
        let column = null;
        if (field.showOnForm == 'readonly') {
            let transformer = field.transformer;

            if (!transformer && field.editor?.type == 'stake') {
                transformer = 'stake2string';
            }
            column = {
                id: row.columnList.length,
                type: 'text',
                field: field.name,
                transformer: transformer,
                width: 5,
            };
            columnCount += 5;
        } else if (field.editor) {
            if (field.editor.type == 'entitySelect') {
                column = {
                    id: row.columnList.length,
                    type: 'entityselect',
                    field: field.name,
                    params: field.editor.params,
                    width: 5
                };
                columnCount += 5;
            } else if (field.editor.type == 'yearPicker') {
                column = {
                    id: row.columnList.length,
                    type: 'yearpicker',
                    field: field.name,
                    width: 5
                };
                columnCount += 5;
            } else if (field.editor.type == 'stake') {
                column = {
                    id: row.columnList.length,
                    type: 'input',
                    transformer: 'stake2string',
                    field: field.name,
                    width: 5
                };
                columnCount += 5;
            } else {
                column = {
                    id: row.columnList.length,
                    type: field.editor.type,
                    transformer: field.transformer,
                    params: field.editor.params,
                    field: field.name,
                    width: 5
                };
                columnCount += 5;
            }
        } else if (field.dictionary) {
            column = {
                id: row.columnList.length,
                type: 'select',
                field: field.name,
                optionList: dictionaryManager.getSelectOptionList(field.dictionary),
                allowClear: field.required !== false,
                width: 5
            };
            columnCount += 5;
        } else if (field.type == 'string') {
            column = {
                id: row.columnList.length,
                type: 'input',
                field: field.name,
                width: 5
            };
            columnCount += 5;
        } else if (field.type == 'date') {
            column = {
                id: row.columnList.length,
                type: 'datepicker',
                field: field.name,
                width: 5
            };
            columnCount += 5;
        } else if (field.type == 'int' || field.type == 'long') {
            column = {
                id: row.columnList.length,
                type: 'inputnumber',
                field: field.name,
                transformer: 'int2string',
                width: 5
            };
            columnCount += 5;
        }
        else if (field.type == 'double') {
            column = {
                id: row.columnList.length,
                type: 'inputnumber',
                field: field.name,
                transformer: 'float2string',
                width: 5
            };
            columnCount += 5;
        }
        if (!column) {
            column = {
                id: row.columnList.length,
                type: 'input',
                field: field.name,
                width: 5
            };
        }

        row.columnList.push(column);
    }
    if (row.columnList.length > 0) {
        rowList.push(row);
    }
    return { rowList };
}

function CRUDDataProviderFactory(Vue, options) {
    (options);
    return {
        create(module, entity, options) {
            var entityDataManager = Vue.$daisyEntityDataManagerFactory.create(module, entity);
            const title = computed(() => unref(entityDataManager.getMetadata())?.title || "");
            const fieldList = computed(() => unref(entityDataManager.getMetadata())?.fieldList || []);
            const validatorList = computed(() => unref(entityDataManager.getMetadata())?.validatorList || []);
            const detailFormLayout = computed(() => {
                var result = unref(entityDataManager.getMetadata())?.formLayout?.detail || getNaiveLayout(unref(fieldList), Vue.$daisyDictionaryManager);
                return result;
            });

            function initializeRecord() {
                let record = {};
                for (let key in unref(entityDataManager.getMetadata()).fieldList) {
                    let field = unref(entityDataManager.getMetadata()).fieldList[key];
                    record[field.name] = field.defaultValue || "";
                }
                if (options?.event?.afterInitializeRecord) {
                    record = options.event.afterInitializeRecord(record);
                }
                return record;
            }

            async function tableLoadData() {
                if (options?.event?.beforeTableLoadData) {
                    let promise = options.event.beforeTableLoadData(arguments);
                    if (promise) {
                        return promise;
                    }
                }
                return entityDataManager.loadData.apply(entityDataManager, arguments);
            }

            async function saveOrUpdate(record) {
                if (options?.event?.beforeSaveOrUpdate) {
                    let promise = options.event.beforeSaveOrUpdate(arguments);
                    if (promise) {
                        return promise;
                    }
                }
                var r = {};
                for (let index = 0; index < unref(fieldList).length; index++) {
                    let field = unref(fieldList)[index];
                    if (record[field.name]) {
                        r[field.name] = record[field.name];
                    }
                }

                if (record.id) {
                    r["__rowVersion"] = record["__rowVersion"];
                }
                return await Vue.$daisyRequest.post(`/${module}/${entity}/saveOrUpdate`, { record: r });
            }

            const recordOperationList = [
                {
                    'id': 0,
                    'operation': 'detail',
                    'title': '详情',
                    show(record) {
                        return !!unref(record) && unref(record).__deleteStatus == 0;
                    },
                    action(record) {
                        this.$emit('edit', { name: 'edit', record: record });
                    },
                },
                {
                    'id': 1,
                    'operation': 'delete',
                    'title': '删除',
                    show(record) {
                        return !!unref(record) && unref(record).__deleteStatus == 0;
                    },
                    prompt(record) {
                        (record);
                        return {
                            "title": "删除",
                            "text": "确定删除",
                        };
                    },
                    action(record) {
                        Vue.$daisyRequest.post(`/${module}/${entity}/softDelete`, { 'recordList': [{ 'id': record.id, '__rowVersion': record.__rowVersion }] })
                            .then(() => {
                                this.refresh();
                            });
                    },
                },
                {
                    'id': 3,
                    'operation': 'recover',
                    'title': '恢复',
                    show(record) {
                        return !!unref(record) && unref(record).__deleteStatus != 0;
                    },
                    prompt(record) {
                        (record);
                        return {
                            "title": "恢复",
                            "text": "确定恢复",
                        };
                    },
                    action(record) {
                        Vue.$daisyRequest.post(`/${module}/${entity}/recoverFromRecycleBin`, { 'recordList': [{ 'id': record.id, '__rowVersion': record.__rowVersion }] })
                            .then(() => {
                                this.refresh();
                            });
                    },
                }];

            async function deleteRecordList(recordList) {
                recordList = recordList.map(r => {
                    return {
                        id: r.id,
                        __rowVersion: r.__rowVersion,
                    }
                });
                await Vue.$daisyRequest.post(`/${module}/${entity}/softDelete`, { recordList });
            }

            return {
                ...(options?.base),
                title,
                validatorList,
                recordOperationList,
                detailFormLayout,
                entityDataManager,
                tableLoadData,
                initializeRecord,
                saveOrUpdate,
                deleteRecordList,
            };
        }
    }
}

const CRUDDataProviderFactoryInstaller = {
    install(Vue, options) {
        Object.defineProperties(Vue.config.globalProperties, {
            $daisyCRUDDataProviderFactory: {
                get: function get() {
                    return CRUDDataProviderFactory(Vue, options);
                }
            }
        });
    }
}

export default CRUDDataProviderFactoryInstaller;
