export default function tableTransformer(registerTransformerFactory) {
    registerTransformerFactory("stakeInCRUDTable", () => {
        return {
            toDisplay: (intValue) => {
                if (isNaN(intValue) || intValue === '') {
                    return '';
                }
                let km = Math.floor(intValue / 100000);
                let m = Math.floor(intValue / 100) % 1000;
                let cm = intValue % 100;

                if (cm == 0) {
                    return 'K' + km + '+' + String(m).padStart(3, '0');
                } else {
                    return 'K' + km + '+' + String(m).padStart(3, '0') + '.' + String(cm).padStart(2, 0);
                }
            }
        };
    });

    registerTransformerFactory("kilometerInCRUDTable", () => {
        return {
            toDisplay: (intValue) => {
                if (isNaN(intValue) || intValue === '') {
                    return '';
                }
                return String(intValue / 1000);
            }
        };
    });

    registerTransformerFactory("meterInCRUDTable", () => {
        return {
            toDisplay: (intValue) => {
                if (isNaN(intValue) || intValue === '') {
                    return '';
                }
                return String(intValue / 100);
            }
        };
    });
}
