<template>
  <a-row :style="{ alignItems: 'center' }">
    <a-col :span="9" :style="{ paddingLeft: '8px', paddingRight: '8px' }">
      <a-typography :style="{ whiteSpace: 'nowrap', overflow: 'hidden', textOverflow: 'ellipsis', textAlign: 'right' }"
        :title="title">{{ title }}</a-typography>
    </a-col>
    <a-col :span="15">
      <a-input-number style="width: 100%" :maxlength="$props.field.editorParameter.size"
        :disabled="$props.field.getReadonly($props.dataContext)" :precision="precision" v-model:value="value" />
      <div v-if="validateMessageList && validateMessageList.length > 0" style="height: 0; overflow: show;">
        <div v-for="validateMessage in validateMessageList" v-bind:key="validateMessage"
          style="font-size: 90%; color: red;">{{ validateMessage }}</div>
      </div>
    </a-col>
  </a-row>
</template>
<script>
import { Col, InputNumber, Row, Typography } from 'ant-design-vue';
import "ant-design-vue/lib/col/style/css";
import "ant-design-vue/lib/input-number/style/css";
import "ant-design-vue/lib/row/style/css";
import "ant-design-vue/lib/typography/style/css";
import { defineComponent } from "vue";

export default defineComponent({
  name: "KNumberFieldEditor",
  props: {
    field: {
      type: Object,
      required: true,
    },
    fieldList: {
      type: Array,
      required: true,
    },
    dataContext: {
      type: Object,
      required: true,
    },
  },
  components: {
    "ATypography": Typography,
    "AInputNumber": InputNumber,
    "ARow": Row,
    "ACol": Col,
  },
  computed: {
    title() {
      return (this.$props.field.mandatory ? '* ' : '') + this.$props.field.title;
    },
    runtimeContext() {
      return this.$props.dataContext.getRuntimeContext(this.$props.fieldList);
    },
    runtimeContextOfField() {
      return this.runtimeContext.field[this.$props.field.name];
    },
    value: {
      get() {
        let value = this.runtimeContextOfField['值'];
        return (typeof (value) == 'undefined' || value == null) ? value : value / Math.pow(10, this.$props.field.editorParameter?.scale || 0);
      },
      set(value) {
        this.runtimeContextOfField['值'] =
          (typeof (value) == 'undefined' || value == null) ?
            value : Math.round(value * Math.pow(10, this.$props.field.editorParameter?.scale || 0));
      }
    },
    validateMessageList() {
      let validateResultList = this.runtimeContextOfField['校验结果'] || [];

      return validateResultList.map(validateResult => validateResult.message);
    },
    precision() {
      if (typeof (this.$props.field.editorParameter?.precision) == 'number') {
        return this.$props.field.editorParameter.precision;
      } else {
        return 0;
      }
    }
  },
});
</script>
