import { ValidateResult } from "@/components/business/StandardField";

export default function validate(dataContext, fieldList, targetField, parameter) {
  if (!targetField.getDirty(dataContext)) {
    return;
  }

  let runtimeContext = dataContext.getRuntimeContext(fieldList);
  let rawValue = runtimeContext.field[targetField.name]['原始值'];

  if (typeof (rawValue) == 'undefined' || rawValue == null || rawValue === '') {
    return;
  }

  if (!(rawValue.match(new RegExp(parameter.regex)))) {
    return new ValidateResult(targetField.title + '格式不正确', null);
  }
}
