<template>
  <div class="container">
    <keep-alive exclude="KStandardForm">
      <component :is="componentName" v-bind="componentData.propList">
        <template v-for="(index, name) in $slots" v-slot:[name]>
          <slot :name="name" />
        </template>
      </component>
    </keep-alive>
  </div>
</template>

<script>
import KStandardForm, { useStandardForm } from "@/components/business/StandardForm.vue";
import KStandardTable, { useStandardTable } from "@/components/business/StandardTable.vue";
import { useEntityDataManager } from "@/misc/EntityDataManager";
import { computed, defineComponent, getCurrentInstance, reactive, unref } from "vue";

export function useStandardPage(component, module, entity) {
  let entityDataManager = component ? component.$daisyEntityDataManagerFactory.create(module, entity) : useEntityDataManager(module, entity);
  // let metadataManager = component ? component.$daisyMetadataManager : useMetadataManager();

  function initializeRecord() {
    let record = {};
    for (let key in unref(entityDataManager.getMetadata()).fieldList) {
      let field = unref(entityDataManager.getMetadata()).fieldList[key];
      record[field.name] = field.defaultValue || "";
    }
    return record;
  }

  let context = reactive({
    propList: {
      module,
      entity,
      initializeRecord: computed(() => context.initializeRecord),
    },
    initializeRecord
  });

  return context;
}

export default defineComponent({
  name: "KStandardPage",
  components: {
    KStandardTable,
    KStandardForm,
  },
  props: {
    module: {
      type: String,
      required: true,
    },
    entity: {
      type: String,
      required: true,
    },
    initializeRecord: {
      type: Function,
      required: true,
    },
  },
  deactivate() {
  },
  computed: {
    componentData() {
      if (this.componentName == KStandardTable.name) {
        return {
          propList: this.standardTable.propList,
        };
      } else if (this.componentName == KStandardForm.name) {
        return {
          propList: this.standardForm.propList,
        };
      }
      return null;
    },
  },
  methods: {
    saveOrUpdate(e) {
      this.providedData.saveOrUpdate.call(this, e.record).then(() => {
        this.componentName = KStandardTable.name;
        this.standardTable.refresh();
        // unref(this.tableRef).refresh(unref(this.tableRef).pagination);
      });
    },
    cancel() {
      this.componentName = KStandardTable.name;
    },
  },
  setup(propList) {
    let standardTable = useStandardTable(null, propList.module, propList.entity);
    let instance = getCurrentInstance();

    standardTable.onCreate = () => {
      let record = propList.initializeRecord();

      context.standardForm = useStandardForm(instance.proxy, propList.module, propList.entity);
      context.standardForm.dataContext.record = record;
      context.standardForm.forceMandatory = true;
      context.componentName = KStandardForm.name;
      context.standardForm.onClose = (isSave) => {
        context.componentName = KStandardTable.name;
        if (isSave) {
          context.standardTable.refresh();
        }
      }
    };
    standardTable.onDetail = (record) => {
      context.standardForm = useStandardForm(instance.proxy, propList.module, propList.entity);
      context.standardForm.dataContext.record = record.__serverRecord;
      context.componentName = KStandardForm.name;
      context.standardForm.onClose = (isSave) => {
        context.componentName = KStandardTable.name;
        if (isSave) {
          context.standardTable.refresh();
        }
      }
    };

    let context = reactive({
      standardTable,
      componentName: KStandardTable.name,
    });

    return context;
  },
});
</script>

<style scoped>
.container {
  position: absolute;
  left: 0px;
  right: 0px;
  top: 0px;
  bottom: 0px;
  text-align: left;
}
</style>
