import { ValidateResult } from "@/components/business/StandardField";

export default function validate(dataContext, fieldList, targetField, parameter) {
  let isToRaw = typeof (parameter.toRaw) != 'undefined';
  let toField;

  if (!isToRaw) {
    toField = fieldList.filter(field => field.name == parameter.toField)[0];
  }

  if (!targetField.getDirty(dataContext) && (!toField || !toField.getDirty(dataContext))) {
    return;
  }

  let runtimeContext = dataContext.getRuntimeContext(fieldList);
  let rawValueLeft = runtimeContext.field[targetField.name]['原始值'];
  let rawValueRight = isToRaw ? parameter.toRaw : runtimeContext.field[parameter.toField]['原始值'];

  if ((typeof (rawValueLeft) == 'undefined' || rawValueLeft == null)
    && (typeof (rawValueRight) == 'undefined' || rawValueRight == null)) {
    return;
  }

  let valueLeft = runtimeContext.field[targetField.name]['原始值'];
  let valueRight = isToRaw ? parameter.toRaw : runtimeContext.field[parameter.toField]['原始值'];

  if (typeof (valueLeft) != typeof (valueRight)) {
    return;
  }

  let result;

  if (valueLeft < valueRight) {
    result = '小于'
  } else if (valueLeft === valueRight) {
    result = '等于'
  } else if (valueLeft > valueRight) {
    result = '大于'
  }

  if (!parameter.acceptResultList.includes(result)) {
    if (isToRaw) {
      return new ValidateResult(targetField.title + '不能' + result + parameter.toRaw);
    } else {
      let rightField = fieldList.filter(field => field.name == parameter.toField)[0];

      return new ValidateResult(targetField.title + '不能' + result + rightField.title, [parameter.toField]);
    }
  }
}
