<script>
import { Col, Row } from 'ant-design-vue';
import "ant-design-vue/lib/col/style/css";
import "ant-design-vue/lib/row/style/css";
import { defineComponent, h } from "vue";

export default defineComponent({
    name: "LayoutPanel",
    components: {
        "ARow": Row,
        "ACol": Col,
    },
    props: {
        'itemList': {
            type: Array,
            required: true
        }
    },
    computed: {
        rowList() {
            let rowList = [];
            if (this.$props.itemList) {
                let itemList = this.$props.itemList;
                let row = { key: rowList.length, itemList: [] };
                let currentSpan = 0;

                for (let i in itemList) {
                    let item = itemList[i];

                    if (item.span + currentSpan > 24) {
                        rowList.push(row);
                        row = { key: rowList.length, itemList: [] };
                        currentSpan = 0;
                    }
                    row.itemList.push({ key: row.itemList.length, item });
                    currentSpan += item.span;
                }
                if (row.itemList.length > 0) {
                    rowList.push(row);
                }
            }

            return rowList;
        },
    },
    render() {
        return h('div', {}, this.rowList.map(row => h(Row, {
            style: "padding: 15px 22px; align-items: center",
            key: row.key,
        }, () => {
            return row.itemList.map((col) => h(Col, {
                span: col.item.span,
                key: col.key,
            }, () => {
                if (typeof(col.item.component) == 'function') {
                    return col.item.component();
                } else {
                    return h(col.item.component, col.item.propList, col.item.slotList);
                }
            }));
        })));

        // return <div>
        //     <a-row style="{'padding': '15px 22px', 'align-items': 'center' }" v-for="row in this.rowList" key="row.id">
        //         <a-col span="8" style="{'padding': '0px 2px' }" v-for="item in row.itemList" key="item.name">
        //             <component ref="item.ref" is="item.component" v-bind="item.propList" v-on="item.onMap"></component>
        //         </a-col>
        //     </a-row>
        // </div>
    }
});
</script>
