<template>
  <a-row :style="{ alignItems: 'center' }">
    <a-col :span="9" :style="{ paddingLeft: '8px', paddingRight: '8px' }">
      <a-typography :style="{ whiteSpace: 'nowrap', overflow: 'hidden', textOverflow: 'ellipsis', textAlign: 'right' }"
        :title="title">{{ title }}</a-typography>
    </a-col>
    <a-col :span="15">
      <a-select v-model:value="value" :disabled="$props.field.getReadonly($props.dataContext)" :mode="mode" :not-found-content="this.fetching ? undefined : '找不到记录'"
        :show-arrow="false" :default-open="false" :filter-option="false" :options="optionList" :show-search="true"
        :on-search="fetchEntityList" style="width: 100%">
      </a-select>
      <div v-if="validateMessageList && validateMessageList.length > 0" style="height: 0; overflow: show;">
        <div v-for="validateMessage in validateMessageList" v-bind:key="validateMessage"
          style="font-size: 90%; color: red;">{{ validateMessage }}</div>
      </div>
    </a-col>
  </a-row>
</template>
<script>
import { Col, Row, Select, Typography } from 'ant-design-vue';
import "ant-design-vue/lib/col/style/css";
import "ant-design-vue/lib/row/style/css";
import "ant-design-vue/lib/select/style/css";
import "ant-design-vue/lib/typography/style/css";
import { debounce } from 'lodash-es';
import { defineComponent, reactive, watchEffect } from "vue";
import format from 'string-format';

export function getNamedValue(dataContext, field, name) {
  if (name == '已选记录列表' || name == 'selectedRecordList') {
    return field.getByFullPath(dataContext, ['intermediate', field.path, '$.editor.selectedRecordList'])[0] || [];
  } else if (name == '已选记录' || name == 'selectedRecord') {
    return field.getByFullPath(dataContext, ['intermediate', field.path, '$.editor.selectedRecord'])[0];
  }
}

export function setNamedValue(dataContext, field, name, value) {
  if (name == '已选记录列表' || name == 'selectedRecordList') {
    field.setByFullPath(dataContext, ['intermediate', field.path, '$.editor.selectedRecordList'], value);
  } else if (name == '已选记录' || name == 'selectedRecord') {
    field.setByFullPath(dataContext, ['intermediate', field.path, '$.editor.selectedRecord'], value);
  }
}

export default defineComponent({
  name: "KDictionaryFieldEditor",
  props: {
    field: {
      type: Object,
      required: true,
    },
    fieldList: {
      type: Array,
      required: true,
    },
    dataContext: {
      type: Object,
      required: true,
    },
  },
  components: {
    "ATypography": Typography,
    "ASelect": Select,
    "ARow": Row,
    "ACol": Col,
  },
  computed: {
    title() {
      return (this.field.mandatory ? '* ' : '') + this.field.title;
    },
    value: {
      get() {
        if (this.mode == 'multiple') {
          return this.runtimeContextOfField['值'];
        } else {
          return this.runtimeContextOfField['值'];
        }
      },
      set(value) {
        if (this.mode == 'multiple') {
          this.runtimeContextOfField['值'] = value;
        } else {
          this.runtimeContextOfField['值'] = value;
        }
      }
    },
    runtimeContext() {
      return this.$props.dataContext.getRuntimeContext(this.$props.fieldList);
    },
    runtimeContextOfField() {
      return this.runtimeContext.field[this.$props.field.name];
    },
    validateMessageList() {
      let validateResultList = this.runtimeContextOfField['校验结果'] || [];

      return validateResultList.map(validateResult => validateResult.message);
    },
    mode() {
      let mode = this.$props.field.editorParameter?.mode;

      mode = mode == 'multiple' ? mode : null;

      return mode;
    },
    entityDataManager() {
      return this.$daisyEntityDataManagerFactory.create(this.$props.field.editorParameter.module, this.$props.field.editorParameter.entity);
    },
    fetchEntityList() {
      let _this = this;
      let editorParameter = this.$props.field.editorParameter;

      return debounce((value) => {
        _this.lastFetchId += 1;
        const fetchId = _this.lastFetchId;
        _this.optionList = [];
        _this.fetching = true;
        _this.entityDataManager.loadData(
          { currentPage: 1, pageSize: 20 },
          [{ 'propertyName': editorParameter.field, 'opType': 'like', 'value': '%' + value + '%' }])
          .then(({ recordList }) => {
            if (fetchId !== _this.lastFetchId) {
              return;
            }

            const optionList = recordList.map(record => ({
              label: format("{0} - {1}", record[editorParameter.field] || '<空>', record[editorParameter.title] || '<空>'),
              value: record[editorParameter.field],
            }));
            let recordMap = {};
            recordList.forEach(record => {
              recordMap[record[editorParameter.field]] = record;
            });
            _this.optionList = optionList;
            _this.fetching = false;
            _this.recordMap = recordMap;
          });
      }, 300);
    }
  },
  mounted() {
    this.fetchEntityList('');
    watchEffect(() => {
      let valueList = this.runtimeContextOfField['值'];

      if (this.mode != 'multiple') {
        valueList = [valueList];
      }

      if (!valueList) {
        this.runtimeContextOfField['已选记录列表'] = [];
        this.runtimeContextOfField['已选记录'] = null;
      } else {
        this.runtimeContextOfField['已选记录列表'] = valueList.map(value => this.recordMap[value]);
        this.runtimeContextOfField['已选记录'] = this.runtimeContextOfField['已选记录列表'][0];
      }
    });
  },
  setup() {
    return reactive({
      optionList: [],
      fetching: false,
      lastFetchId: 0,
      recordMap: {},
    });
  },
});
</script>
