<script>
import { Col, Row } from 'ant-design-vue';
import "ant-design-vue/lib/col/style/css";
import "ant-design-vue/lib/row/style/css";
import { defineComponent, h, reactive, computed, watch, watchEffect } from "vue";

export function useFieldLayoutPanel() {
  let context = reactive({
    propList: {
      dataContext: computed(() => context.dataContext || {}),
      fieldList: computed(() => context.fieldList || []),
      columnPerRow: computed(() => context.columnPerRow),
    },
    dataContext: computed(() => { return {}; }),
    fieldList: computed(() => []),
    columnPerRow: 3,
  });

  return context;
}

export default defineComponent({
  name: "KFieldLayoutPanel",
  components: {
    "ARow": Row,
    "ACol": Col,
  },
  props: {
    dataContext: {
      type: Object,
      required: true,
    },
    fieldList: {
      type: Array,
      required: true,
    },
    columnPerRow: {
      type: Number,
      required: true,
    },
  },
  computed: {
    groupList() {
      let groupList = [];
      let defaultSpan = 24 / this.$props.columnPerRow;

      if (this.$props.fieldList) {
        let fieldList = this.$props.fieldList;
        let group = null;
        let row = { key: 0, columnList: [] };
        let currentSpan = 0;

        for (let i in fieldList) {
          let field = fieldList[i];
          let fieldSpan = defaultSpan;

          let width = field.width?.split(',') || [];

          if (width.length > 0) {
            if (width[0] == "Row") {
              fieldSpan = 24;
            } else if (width[0] == 'Col') {
              let colSpan = width.length > 1 ? parseInt(width[1]) : 1;

              fieldSpan = Math.min(24, colSpan * defaultSpan);
            }
          }
          if (!group || field.group != group.title) {
            if (group) {
              if (row.columnList.length > 0) {
                group.rowList.push(row);
              }
              groupList.push(group);
            }
            currentSpan = 0;
            group = { title: field.group, key: groupList.length, rowList: [] };
            row = { key: group.rowList.length, columnList: [] };
          }
          if (fieldSpan + currentSpan > 24) {
            group.rowList.push(row);
            row = { key: group.rowList.length, columnList: [] };
            currentSpan = 0;
          }
          row.columnList.push({ key: row.columnList.length, field, span: fieldSpan });
          currentSpan += fieldSpan;
        }
        if (row.columnList.length > 0) {
          group.rowList.push(row);
        }
        if (group?.rowList.length > 0) {
          groupList.push(group);
        }
      }

      return groupList;
    },
  },
  mounted() {
    watch(
      () => this.$props.fieldList,
      () => {
        for (let i in this.$props.fieldList) {
          let field = this.$props.fieldList[i];
          watchEffect(() => field.validate(this.$props.dataContext, this.$props.fieldList));
        }
      },
      {
        immediate: true,
      });
  },
  render() {
    return h(
      'div',
      {
        style: "padding: 8px",
      },
      this.groupList.map(
        group => h(
          'div',
          {
            style: "margin: 22px 8px",
          },
          [
            h(
              'div',
              {
                style: "padding: 15px 22px; align-items: center" + (group.title ? "" : "; display: none"),
              },
              group.title
            ),
            h(
              Row,
              {
                style: "padding: 15px 22px; align-items: center",
                key: group.key * 2 + 1,
              },
              () => group.rowList.map(
                row => h(
                  Col,
                  {
                    span: 24,
                    key: row.key,
                  },
                  () => h(
                    Row,
                    {
                      style: "padding: 15px 22px; align-items: center",
                      key: row.key,
                    },
                    () => row.columnList.map(
                      column => h(
                        Col,
                        {
                          span: column.span,
                          key: column.key,
                        },
                        () => h(
                          column.field.getComponent(this.$props.dataContext),
                          column.field.getPropList(this.$props.dataContext, this.$props.fieldList),
                          column.field.getSlotList(this.$props.dataContext)
                        )
                      )
                    )
                  )
                )
              )
            )
          ]
        )
      )
    );
  },

  // return <div>
  //     <a-row style="{'padding': '15px 22px', 'align-items': 'center' }" v-for="row in this.rowList" key="row.id">
  //         <a-col span="8" style="{'padding': '0px 2px' }" v-for="item in row.itemList" key="item.name">
  //             <component ref="item.ref" is="item.component" v-bind="item.propList" v-on="item.onMap"></component>
  //         </a-col>
  //     </a-row>
  // </div>
});
</script>
