import { ref, inject } from 'vue';
import { ENTITY_DATA_MANAGER_FACTORY } from "@/misc/Symbol";

class EntityDataManager {
  constructor(Vue, module, entity) {
    this.Vue = Vue;
    this.module = module;
    this.entity = entity;
    this.metadataFetched = false;
    this.metadata = ref({});
  }

  getMetadata() {
    return this.metadata;
  }

  async deleteRecordList(recordList) {
    let result = await this.Vue.$daisyRequest.post(`/${this.module}/${this.entity}/softDelete`, { recordList });
    return result;
  }

  async recoverRecordList(recordList) {
    let result = await this.Vue.$daisyRequest.post(`/${this.module}/${this.entity}/recoverFromRecycleBin`, { recordList });
    return result;
  }

  async exportData(filename, format, selectedIdList, selectedFieldList) {
    var result = await this.Vue.$daisyRequest.post(`/${this.module}/${this.entity}/export`, { filename, format, selectedIdList, selectedFieldList });
    return result;
  }

  async saveOrUpdate(oldRecord, newRecord) {
    if (oldRecord['id']) {
      newRecord["id"] = oldRecord["id"];
      newRecord["__rowVersion"] = oldRecord["__rowVersion"];
    }
    return await this.Vue.$daisyRequest.post(`/${this.module}/${this.entity}/saveOrUpdate`, { record: newRecord });
  }

  async loadData(pagination, criteria, showSoftDeleted = false, orderBy = []) {
    let { currentPage, pageSize } = pagination;
    if (!this.metadataFetched) {
      this.metadata.value = await this.Vue.$daisyMetadataManager.get(this.module, this.entity);
      var dictionaryList = [];
      for (let index in this.metadata.value.fieldList) {
        let field = this.metadata.value.fieldList[index];
        if (field.dictionary != null) {
          dictionaryList.push(field.dictionary);
        }
      }
      await this.Vue.$daisyDictionaryManager.load(dictionaryList);

      this.metadataFetched = true;
    }
    var result = await this.Vue.$daisyRequest.post(`/${this.module}/${this.entity}/queryWithPage`, { pageNo: currentPage, pageSize, criteria, showSoftDeleted, orderBy });
    var serverRecordList = result.data.data.recordList;
    var total = result.data.data.totalPage * pageSize;
    return {
      recordList: serverRecordList,
      total
    };
  }
}

function EntityDataManagerFactory(Vue, options) {
  (options);
  return {
    create(module, entity) {
      return new EntityDataManager(Vue, module, entity);
    }
  };
}

/**
 * 注意这个方法只能在setup函数中调用，其他时候调用会返回undefined
 * @returns EntityDataManager的实例
 */
export function useEntityDataManager(module, entity) {
  return inject(ENTITY_DATA_MANAGER_FACTORY).create(module, entity);
}

const EntityDataManagerFactoryInstaller = {
  install(Vue, options) {
    var entityDataManagerFactory = EntityDataManagerFactory(Vue, options);
    Vue.$daisyEntityDataManagerFactory = entityDataManagerFactory;
    Vue.provide(ENTITY_DATA_MANAGER_FACTORY, entityDataManagerFactory);

    Object.defineProperties(Vue.config.globalProperties, {
      $daisyEntityDataManagerFactory: {
        get() {
          return entityDataManagerFactory;
        }
      }
    });
  }
}

export default EntityDataManagerFactoryInstaller;
