<template>
  <div class="root">
    <!-- <div class="header">
      <span>表单填写</span>
    </div> -->
    <div class="content">
      <k-field-layout-panel v-bind="fieldLayoutPanel.propList" />
    </div>
    <div class="footer">
      <slot name="footer">
        <a-space :size="9" align="end">
          <a-button type="primary" :disabled="hasValidateError || !dirty" @click="handleSave">保存</a-button>
          <a-button @click="handleCancel">关闭</a-button>
        </a-space>
      </slot>
    </div>
    <div>
      <k-message-box-provider v-bind="messageBoxProvider.propList"></k-message-box-provider>
    </div>
  </div>
</template>

<script>
import DataContext from "@/components/business/DataContext";
import FieldLayoutPanel, { useFieldLayoutPanel } from '@/components/business/FieldLayoutPanel.vue';
import { getStandandFieldFromMetadataField } from "@/components/business/StandardField";
import KMessageBoxProvider, { useMessageBoxProvider } from '@/components/MessageBoxProvider.vue';
import { useEntityDataManager } from "@/misc/EntityDataManager";
import { useMetadataManager } from "@/misc/MetadataManager";
import { Button, Space } from 'ant-design-vue';
import "ant-design-vue/lib/button/style/css";
import "ant-design-vue/lib/message/style/css";
import { computed, defineComponent, reactive, watchEffect, watch } from "vue";

export function useStandardForm(component, module, entity) {
  let onSaveOrUpdate = () => { context.onClose && context.onClose(true); };

  if (module && entity) {
    let entityDataManager = component ? component.$daisyEntityDataManagerFactory.create(module, entity) : useEntityDataManager(module, entity);
    let metadataManager = component ? component.$daisyMetadataManager : useMetadataManager();

    watchEffect(() => {
      metadataManager.get(module, entity)
        .then(metadata => context.metadata = metadata);
    });

    onSaveOrUpdate = function (e) {
      entityDataManager.saveOrUpdate(context.dataContext.record, e.record).then(() => {
        context.onClose && context.onClose(true);
      });
    }
  }

  function onCancel() {
    context.onClose && context.onClose(false);
  }

  let context = reactive({
    propList: {
      dataContext: computed(() => context.dataContext || {}),
      fieldList: computed(() => context.fieldList || []),
      forceMandatory: computed(() => context.forceMandatory),
      onSaveOrUpdate: computed(() => context.onSaveOrUpdate),
      onCancel: computed(() => context.onCancel),
    },
    dataContext: new DataContext(),
    fieldList: computed(() => {
      let fieldList = context.metadata?.fieldList.map(field => getStandandFieldFromMetadataField(field, context.metadata.validatorList)).filter(field => field) || [];

      return fieldList;
    }),
    metadata: null,
    forceMandatory: false,
    onSaveOrUpdate,
    onCancel,
    onClose: null,
  });

  return context;
}

export default defineComponent({
  name: "KStandardForm",
  components: {
    'AButton': Button,
    'ASpace': Space,
    'KFieldLayoutPanel': FieldLayoutPanel,
    KMessageBoxProvider,
  },
  emits: ["saveOrUpdate", "cancel"],
  props: {
    dataContext: {
      type: Object,
      required: true,
    },
    fieldList: {
      type: Array,
      required: true,
    },
    forceMandatory: {
      type: Boolean,
    },
  },
  activated() {
  },
  deactivated() {
  },
  computed: {
    runtimeContext() {
      return this.$props.dataContext.getRuntimeContext(this.$props.fieldList);
    },
    hasValidateError() {
      return this.$props.fieldList.filter(field => this.runtimeContext.field[field.name]['校验结果'].length > 0).length > 0;
    },
    dirty() {
      return this.$props.fieldList.filter(field => this.runtimeContext.field[field.name]['更新过标志']).length > 0;
    },
  },
  methods: {
    handleSave() {
      if (this.$props.forceMandatory) {
        this.$props.dataContext.forceMandatory = true;
        this.$props.fieldList.forEach(field => field.validate(this.$props.dataContext, this.$props.fieldList));
        if (this.hasValidateError) {
          return;
        }
      }

      this.messageBoxProvider.confirm(
        this.$daisyStringManager.format("daisy.confirm.saveOrUpdateText"),
        this.$daisyStringManager.format("daisy.confirm.saveOrUpdateTitle")
      ).then(() => {
        this.$emit('saveOrUpdate', {
          name: 'saveOrUpdate',
          record: this.$props.dataContext.updatedRecord,
        });
      });
    },
    handleCancel() {
      if (this.dirty) {
        this.messageBoxProvider.confirm(
          this.$daisyStringManager.format("daisy.confirm.cancelText"),
          this.$daisyStringManager.format("daisy.confirm.cancelTitle")
        ).then(() => {
          this.$emit('cancel', { name: 'cancel', });
        });
      } else {
        this.$emit('cancel', { name: 'cancel', });
      }
    },
  },
  setup(propList) {
    const fieldLayoutPanel = useFieldLayoutPanel();
    const watchEffectDisposerList = [];
    fieldLayoutPanel.fieldList = computed(() => propList.fieldList);
    fieldLayoutPanel.dataContext = computed(() => propList.dataContext);

    let context = reactive({
      messageBoxProvider: useMessageBoxProvider(),
      fieldLayoutPanel,
      watchEffectDisposerList,
    });

    watch(
      () => propList.fieldList,
      (fieldList) => {
        if (!fieldList) {
          return;
        }

        let runtimeContext = propList.dataContext.getRuntimeContext(fieldList);
        let watchEffectDisposerList = [];

        fieldList.forEach(field => {
          if (!field.calculationList) {
            return;
          }

          field.calculationList.forEach(calculation => {
            let targetNamedValue = calculation.targetNamedValue || '值';

            let disposer = watchEffect(() => {
              if (calculation.check) {
                if (calculation.check.split(',').filter(fieldName => runtimeContext.field[fieldName]['更新过标志']).length == 0) {
                  return;
                }
              }

              function evalWithContext(runtimeContext, code) {
                'use strict';

                const { field } = runtimeContext;

                (field);
                return eval(code);
              }

              runtimeContext.field[field.name][targetNamedValue] = evalWithContext(runtimeContext, calculation.formula);
            });

            watchEffectDisposerList.push(disposer);
          });
        });

        context.watchEffectDisposerList.forEach(disposer => disposer());
        context.watchEffectDisposerList = watchEffectDisposerList;
      },
      {
        immediate: true,
      });

    return context;
  },
});
</script>

<style scoped>
.root {
  width: 100%;
  height: 100%;
  padding: 8px;
  position: absolute;
  display: flex;
  flex-direction: column;
}

.content {
  overflow: hidden auto;
  flex: 1 1 auto;
}

.footer {
  flex: 0 0 auto;
  text-align: right;
  padding-right: 59px;
}
</style>
