<template>
    <a-row :style="{ alignItems: 'center' }">
        <a-col :span="9" :style="{ paddingLeft: '8px', paddingRight: '8px' }">
            <a-typography
                :style="{ whiteSpace: 'nowrap', overflow: 'hidden', textOverflow: 'ellipsis', textAlign: 'right' }"
                :title="title">{{ title }}</a-typography>
        </a-col>
        <a-col :span="15">
            <a-input disabled :maxlength="field.size" v-model:value="value" />
        </a-col>
    </a-row>
</template>
<script>
import { Col, Row, Typography, Input } from 'ant-design-vue';
import "ant-design-vue/lib/col/style/css";
import "ant-design-vue/lib/row/style/css";
import "ant-design-vue/lib/typography/style/css";
import "ant-design-vue/lib/input/style/css";

import { defineComponent } from "vue";

export default defineComponent({
    name: "ReadonlyFieldEditor",
    props: {
        field: {
            type: Object,
            required: true,
        },
        record: {
            type: Object,
            required: true,
        },
    },
    components: {
        "ATypography": Typography,
        "ARow": Row,
        "ACol": Col,
        "AInput": Input,
    },
    computed: {
        title() {
            return this.field.title;
        },
        value() {
            return this.record[this.field.name];
        }
    },
});
</script>