export default (record, columnList, args, tip) => {
  const message = {
    recordMessage: '',
    columnMessage: []
  }

  for (let column of columnList) {
    if (typeof(record[column]) == typeof(undefined) || record[column] === '') {
      message.columnMessage.push({
        column: column,
        message: tip || '不能为空'
      })
    }
  }
  return message;
}
