<template>
    <a-row :style="{ alignItems: 'center' }">
        <a-col :span="9" :style="{ paddingLeft: '8px', paddingRight: '8px' }">
            <a-typography
                :style="{ whiteSpace: 'nowrap', overflow: 'hidden', textOverflow: 'ellipsis', textAlign: 'right' }"
                :title="title">{{ title }}</a-typography>
        </a-col>
        <a-col :span="15">
            <a-select v-model:value="value" :not-found-content="this.fetching ? undefined : '找不到记录'" :show-arrow="false"
                :default-open="false" :filter-option="false" :options="optionList" :show-search="true"
                :on-search="fetchEntity" style="width: 100%">
            </a-select>
            <div v-if="field.validateMessage" style="height: 0; overflow: show;">
                <span style="font-size: 90%; color: red;">{{ field.validateMessage }}</span>
            </div>
        </a-col>
    </a-row>
</template>
<script>
import { Col, Row, Select, Typography } from 'ant-design-vue';
import "ant-design-vue/lib/col/style/css";
import "ant-design-vue/lib/row/style/css";
import "ant-design-vue/lib/select/style/css";
import "ant-design-vue/lib/typography/style/css";
import { debounce } from 'lodash-es';
import { defineComponent, reactive, toRefs } from "vue";

export default defineComponent({
    name: "DictionaryFieldEditor",
    props: {
        field: {
            type: Object,
            required: true,
        },
        record: {
            type: Object,
            required: true,
        },
    },
    emits: ['fieldChange'],
    components: {
        "ATypography": Typography,
        "ASelect": Select,
        "ARow": Row,
        "ACol": Col,
    },
    computed: {
        title() {
            return (this.field.required ? '* ' : '') + this.field.title;
        },
        value: {
            get() {
                return this.record[this.field.name];
            },
            set(value) {
                let record = this.record;
                record[this.field.name] = value;
                this.$emit("fieldChange", { field: this.field });
            }
        },
        entityDataManager() {
            return this.$daisyEntityDataManagerFactory.create(this.field.editor.params.module, this.field.editor.params.entity);
        },
        fetchEntity() {
            var _this = this;
            return debounce((value) => {
                _this.lastFetchId += 1;
                const fetchId = _this.lastFetchId;
                _this.optionList = [];
                _this.fetching = true;
                _this.entityDataManager.loadData(
                    { currentPage: 1, pageSize: 20 },
                    [{ 'propertyName': _this.field.editor.params.field, 'opType': 'like', 'value': '%' + value + '%' }])
                    .then(({ recordList }) => {
                        if (fetchId !== _this.lastFetchId) {
                            // for fetch callback order
                            return;
                        }

                        const optionList = recordList.map(record => ({
                            label: `${record[_this.field.editor.params.field]} ${record[_this.field.editor.params.show]}`,
                            value: record[_this.field.editor.params.field],
                        }));
                        _this.optionList = optionList;
                        _this.fetching = false;
                    });
            }, 300);
        }
    },
    setup() {
        let lastFetchId = 0;
        const state = reactive({
            optionList: [],
            fetching: false,
        });
        return {
            ...toRefs(state),
            lastFetchId,
        };
    },
});
</script>