import DaisyCRUDInside from "../components/DaisyCRUD.vue";
import CRUDDataProviderFactory from "../misc/CRUDDataProvider";
import DictionaryManager from "../misc/DictionaryManager";
import EntityDataManagerFactory from "../misc/EntityDataManager";
import MetadataManager from "../misc/MetadataManager";
import StringManager from "../misc/StringManager";
import ValidatorManager from "../misc/ValidatorManager";

const Daisy = {
  "name": "Daisy",
  install(Vue, options) {
    Vue.$daisyRequest = options.request;
    Object.defineProperties(Vue.config.globalProperties, {
      $daisyRequest: {
        get() {
          return options.request;
        }
      }
    });
  }
};

export default {
  name: "Daisy.CRUD",
  install(Vue, options) {
    Vue.use(Daisy, options);
    Vue.use(DictionaryManager, options);
    Vue.use(MetadataManager, options);
    Vue.use(StringManager, options);
    Vue.use(EntityDataManagerFactory, options);
    Vue.use(CRUDDataProviderFactory, options);
    Vue.use(ValidatorManager, options);

    Vue.component("DaisyCRUD", DaisyCRUDInside);
  }
};
