<template>
    <a-row :style="{ alignItems: 'center' }">
        <a-col :span="9" :style="{ paddingLeft: '8px', paddingRight: '8px' }">
            <a-typography
                :style="{ whiteSpace: 'nowrap', overflow: 'hidden', textOverflow: 'ellipsis', textAlign: 'right' }"
                :title="title">{{ title }}</a-typography>
        </a-col>
        <a-col :span="15">
            <a-select v-model:value="value" style="width: 100%" :allowClear="true" :options="optionList"></a-select>
        </a-col>
    </a-row>
</template>
<script>
import { defineComponent, ref } from "vue";
import { Row, Col, Typography, Select } from 'ant-design-vue';
import { useDictionaryManager } from '@/misc/DictionaryManager';
import "ant-design-vue/lib/typography/style/css";
import "ant-design-vue/lib/select/style/css";
import "ant-design-vue/lib/row/style/css";
import "ant-design-vue/lib/col/style/css";

export default defineComponent({
    name: "DictionaryConditionEditor",
    props: {
        field: {
            type: Object,
            required: true,
        }
    },
    emits: ['update:criteriaList'],
    components: {
        "ATypography": Typography,
        "ASelect": Select,
        "ARow": Row,
        "ACol": Col,
    },
    computed: {
        title() {
            return this.$props.field.title;
        },
        value: {
            get() {
                return this.internalValue;
            },
            set(value) {
                if (value) {
                    this.$emit("update:criteriaList", [{
                        propertyName: this.$props.field.name,
                        opType: 'equals',
                        value: `${value}`,
                    }]);
                } else {
                    this.$emit("update:criteriaList", []);
                }
                this.internalValue = value;
            }
        },
    },
    methods: {
        reset() {
            this.value = '';
        }
    },
    setup(props) {
        let dictionaryManager = useDictionaryManager();
        let optionList = ref([]);

        dictionaryManager.load([props.field.dictionary]).then(() => {
            optionList.value = dictionaryManager.getSelectOptionList(props.field.dictionary);
        })

        return {
            optionList,
            internalValue: ref()
        };
    },
});
</script>