<template>
  <a-drawer v-model:visible="drawerVisible" :title="title" placement="right">
    <div style="height: 100%; overflow: hidden; display: flex; flex-flow: column;">
      <div style="flex: 0 0 auto; padding: 8px">
        <a-checkbox v-model:checked="checkAll" :indeterminate="indeterminate" @change="handleCheckAllChange">选择全部
        </a-checkbox>
        <a-divider />
      </div>
      <div style="flex: 1 1 auto; padding: 8px; overflow: hidden scroll">
        <div v-for="field in internalFieldList" :key="field.key">
          <a-checkbox v-model:checked="field.visible" :disabled="field.readonly">{{ field.title }}</a-checkbox>
        </div>
      </div>
      <div style="flex: 0 0 auto; text-align: right; padding: 8px">
        <a-space>
          <a-button type="primary" @click="handleOk">确定</a-button>
          <a-button @click="handleCancel">取消</a-button>
        </a-space>
      </div>
    </div>
  </a-drawer>
</template>
<script>
import { defineComponent, reactive } from 'vue';
import { Drawer, Space, Button, Checkbox, Divider } from 'ant-design-vue';
import "ant-design-vue/lib/drawer/style/css";
import "ant-design-vue/lib/button/style/css";
import "ant-design-vue/lib/space/style/css";
import "ant-design-vue/lib/checkbox/style/css";
import "ant-design-vue/lib/divider/style/css";

export function useFieldChooser() {
  let showPromiseResolve = null;
  let showPromiseReject = null;

  function handleOk(fieldList) {
    showPromiseResolve(fieldList);
    propList.visible = false;
  }

  function handleCancel() {
    showPromiseReject();
    propList.visible = false;
    showPromiseResolve = null;
    showPromiseReject = null;
  }

  const propList = reactive({
    fieldList: [],
    visible: false,
    'onUpdate:visible': (value) => {
      if (!propList.visible) {
        handleCancel();
      } else {
        propList.visible = value;
      }
    },
    onOk: handleOk,
    onCancel: handleCancel,
  });

  return {
    propList,
    show(fieldList) {
      propList.fieldList = fieldList;
      propList.visible = true;
      return new Promise((resolve, reject) => {
        showPromiseResolve = resolve;
        showPromiseReject = reject;
      });
    },
  }
}

export default defineComponent({
  name: 'FieldChooser',
  components: {
    ADrawer: Drawer,
    AButton: Button,
    ASpace: Space,
    ACheckbox: Checkbox,
    ADivider: Divider,
  },
  props: {
    title: {
      type: String,
    },
    fieldList: {
      type: Array,
      required: true,
    },
    visible: {
      type: Boolean,
      require: true,
    },
  },
  emits: ['ok', 'cancel'],
  computed: {
    drawerVisible: {
      get() {
        return this.visible;
      },
      set(value) {
        this.$emit('update:visible', value);
      },
    },
    internalFieldList() {
      return reactive(this.fieldList.map(field => { return { ...field }; }));
    },
    indeterminate() {
      let visibleCount = 0;
      let readonlyCount = 0;

      this.internalFieldList.forEach(field => {
        visibleCount += field.visible ? 1 : 0;
        readonlyCount += field.readonly ? 1 : 0;
      });

      return this.internalFieldList.length != visibleCount && visibleCount != readonlyCount;
    },
    checkAll: {
      get() {
        let visibleCount = 0;

        this.internalFieldList.forEach(field => {
          visibleCount += field.visible ? 1 : 0;
        });

        return this.internalFieldList.length == visibleCount;
      },
      set(value) {
        this.internalFieldList.forEach(field => {
          if (!field.readonly) {
            field.visible = value;
          }
        });
      }
    },
  },
  methods: {
    handleOk() {
      this.$emit('ok', this.internalFieldList);
    },
    handleCancel() {
      this.$emit('cancel');
    },
    handleCheckAllChange(e) {
      (e);
    },
  },
});
</script>
