<template>
  <div class="container">
    <div class="condition-panel">
      <query-condition-panel v-bind="queryConditionPanel.propList" />
    </div>
    <div class="toolbar">
      <slot name="toolbar">
        <a-space :size="10">
          <a-button type="primary" v-if="this.$props.buttonList?.includes('createNew') && !this.showSoftDeleted"
            @click="this.$emit('toolbarButtonClick', { name: 'createNew' })">
            <plus-outlined />新建
          </a-button>
          <a-button type="primary" v-if="this.$props.buttonList?.includes('import') && !this.showSoftDeleted" disabled>
            <import-outlined />导入
          </a-button>
          <a-button danger
            v-if="this.$props.buttonList?.includes('deleteSelectedItemList') && !this.showSoftDeleted && this.deleteItemList"
            :disabled="selectedCount == 0" @click="() => this.deleteSelectedItem()">
            <delete-outlined />删除
          </a-button>
          <a-button v-if="this.$props.buttonList?.includes('toggleRecycle')"
            type="{ this.showSoftDeleted ? 'primary' : '' }" @click="() => this.toggleShowSoftDeleted()">
            <delete-outlined />{{ this.showSoftDeleted ? '返回列表' : '回收站' }}
          </a-button>
          <a-button v-if="this.$props.buttonList?.includes('export') && !this.showSoftDeleted && this.exportData"
            @click="this.tableExportModal.show()">
            <export-outlined />导出
          </a-button>
          <a-button v-if="this.$props.buttonList?.includes('chooseField')" @click="showFieldChooser">
            <unordered-list-outlined />选择列
          </a-button>
        </a-space>
      </slot>
    </div>
    <a-selected-item-list-indicator v-bind="selectedItemListIndicator.propList" />
    <div class="table">
      <a-table :columns="tableFieldList" :dataSource="recordList" :pagination="internalPagination" :loading="loading"
        :row-selection="{
          selectedRowKeys: selectedKeyListInPage,
          onChange: handleSelectionChange,
        }" @change="handleChange" @resizeColumn="handleResizeColumn" bordered size="small" :scroll="{ x: -1, y: -1 }">
        <template #bodyCell="args">
          <template v-if="args.column.key === '__operationList'">
            <slot name="tableOperationCell" v-bind="args">
              <div style="position: absolute; top: 8px; left: 8px; bottom: 8px; right: 8px; overflow: hidden">
                <a-button type="link" size="small"
                  v-if="this.$props.buttonList?.includes('showDetail') && !this.showSoftDeleted"
                  @click="this.$emit('itemButtonClick', { name: 'detail', item: args.record })">
                  详情
                </a-button>
                <a-button type="link" size="small"
                  v-if="this.$props.buttonList?.includes('deleteItem') && this.deleteItemList && !this.showSoftDeleted"
                  @click="() => this.deleteItem(args.record)">
                  删除
                </a-button>
                <a-button type="link" size="small"
                  v-if="this.$props.buttonList?.includes('recoverItem') && this.recoverItemList && this.showSoftDeleted"
                  @click="() => this.recoverItem(args.record)">
                  恢复
                </a-button>
              </div>
            </slot>
          </template>
          <template v-else>
            <slot name="tableBodyCell" v-bind="args"> </slot>
          </template>
        </template>
      </a-table>
    </div>
  </div>
  <a-confirm v-bind="confirm.propList" />
  <field-chooser v-bind="fieldChooser.propList" />
  <table-export-modal v-if="tableExportModal.visible" v-bind="tableExportModal.propList" />
</template>

<script>
import Confirm, { useConfirm } from "@/components/Confirm.vue";
import FieldChooser, { useFieldChooser } from "@/components/crud/FieldChooser.vue";
import QueryConditionPanel, { useQueryConditionPanel } from "@/components/crud/QueryConditionPanel.vue";
import SelectedItemListIndicator, { useSelectedItemListIndicator } from "@/components/crud/SelectedItemListIndicator.vue";
import TableExportModal, { useTableExportModal } from "@/components/crud/TableExportModal.vue";
import { useEntityDataManager } from "@/misc/EntityDataManager";
import { useMetadataManager } from "@/misc/MetadataManager";
import { getTransformer } from "@/misc/Transform";
import { DeleteOutlined, ExportOutlined, ImportOutlined, PlusOutlined, UnorderedListOutlined } from '@ant-design/icons-vue';
import { Button, Space, Table } from 'ant-design-vue';
import "ant-design-vue/lib/button/style/css";
import "ant-design-vue/lib/space/style/css";
import "ant-design-vue/lib/table/style/css";
import { computed, defineComponent, reactive, ref, unref, watchEffect } from "vue";

export function useStandardTable(component, module, entity) {
  let entityDataManager = component ? component.$daisyEntityDataManagerFactory.create(module, entity) : useEntityDataManager(module, entity);
  let metadataManager = component ? component.$daisyMetadataManager : useMetadataManager();

  watchEffect(() => {
    metadataManager.get(module, entity)
      .then(metadata => context.metadata = metadata);
  });

  function loadData() {
    return entityDataManager.loadData.apply(entityDataManager, Array.prototype.slice.call(arguments))
      .then((data) => {
        let tableRecordList = [];

        for (let recordIndex = 0; recordIndex < data.recordList.length; recordIndex++) {
          let serverRecord = data.recordList[recordIndex];
          let tableRecord = { key: serverRecord.id };

          for (let index = 0; index < unref(context.fieldList)?.length; index++) {
            let field = unref(context.fieldList)[index];
            let transformer = getTransformer(field.transformer) || getTransformer(field.fieldType + 'InCRUDTable') || getTransformer('noop');

            if (field.dictionary) {
              tableRecord[field.name] = transformer.toDisplay(this.$daisyDictionaryManager.getTitle(field.dictionary, serverRecord[field.name]) || '');
            } else {
              tableRecord[field.name] = transformer.toDisplay(serverRecord[field.name]) || '';
            }
          }
          tableRecord.__serverRecord = serverRecord;
          tableRecordList.push(tableRecord);
        }
        return { recordList: tableRecordList, totalRecordCount: data.total };
      })
  }

  function exportData() {
    return entityDataManager.exportData.apply(entityDataManager, Array.prototype.slice.call(arguments))
      .then(response => {
        return response.data.data.downloadUrl;
      });
  }

  function deleteItemList(itemList) {
    itemList = itemList.map(r => {
      return {
        id: r.id,
        __rowVersion: r.__rowVersion,
      }
    });
    return entityDataManager.deleteRecordList(itemList);
  }

  function recoverItemList(itemList) {
    itemList = itemList.map(r => {
      return {
        id: r.id,
        __rowVersion: r.__rowVersion,
      }
    });
    return entityDataManager.recoverRecordList(itemList);
  }

  function onToolbarButtonClick(e) {
    if (e.name == 'createNew') {
      context.onCreate && context.onCreate();
    }
  }

  function onItemButtonClick(e) {
    if (e.name == 'detail') {
      context.onDetail && context.onDetail(e.item);
    } else if (e.name == 'recover') {
      context.onRecover && context.onRecover([e.item]);
    }
  }

  function refresh() {
    context.table.updateId++;
  }

  let context = reactive({
    propList: {
      entityDisplayName: computed(() => context.entityDisplayName || ''),
      buttonList: computed(() => context.buttonList || []),
      criteriaList: computed(() => context.criteriaList),
      loadData: computed(() => context.loadData),
      exportData: computed(() => context.exportData),
      deleteItemList: computed(() => context.deleteItemList),
      recoverItemList: computed(() => context.recoverItemList),
      fieldList: computed(() => context.fieldList || []),
      onToolbarButtonClick: computed(() => context.onToolbarButtonClick),
      onItemButtonClick: computed(() => context.onItemButtonClick),
      onMount: (e) => context.table = e.table,
    },
    slotList: [],
    onToolbarButtonClick,
    onItemButtonClick,
    entityDisplayName: computed(() => context.metadata?.title),
    fieldList: computed(() => context.metadata?.fieldList),
    buttonList: [
      'createNew',
      'import',
      'deleteSelectedItemList',
      'toggleRecycle',
      'export',
      'chooseField',
      'showDetail',
      'deleteItem',
      'recoverItem'
    ],
    metadata: null,
    criteriaList: [],
    table: null,
    loadData,
    exportData,
    deleteItemList,
    recoverItemList,
    onCreate: null,
    onDetail: null,
    refresh,
  });

  return context;
}

export default defineComponent({
  name: "KStandardTable",
  components: {
    PlusOutlined,
    ImportOutlined,
    ExportOutlined,
    DeleteOutlined,
    UnorderedListOutlined,
    'AConfirm': Confirm,
    'QueryConditionPanel': QueryConditionPanel,
    'FieldChooser': FieldChooser,
    'TableExportModal': TableExportModal,
    'ATable': Table,
    'AButton': Button,
    'ASpace': Space,
    'ASelectedItemListIndicator': SelectedItemListIndicator,
  },
  emits: ["toolbarButtonClick", "itemButtonClick", "mount"],
  props: {
    entityDisplayName: {
      type: String,
      required: true,
    },
    buttonList: {
      type: Array,
    },
    loadData: {
      type: Function,
      required: true,
    },
    exportData: {
      type: Function,
    },
    deleteItemList: {
      type: Function,
    },
    recoverItemList: {
      type: Function,
    },
    fieldList: {
      type: Array,
      required: true,
    },
    criteriaList: {
      type: Array,
    },
  },
  computed: {
    tableFieldList() {
      const columnList = [];
      for (var index = 0; index < unref(this.fieldList).length; index++) {
        let field = unref(this.fieldList)[index];
        if (field.showOnList == "disable") {
          continue;
        }
        if (field.showOnList == "hidden" && !this.fieldVisiableStateMap[field.name]) {
          continue;
        }

        let width = field.width;

        if (!width || width == 0) {
          if (isNaN(field.size)) {
            width = 100;
          } else if (field.fieldType == 'stake'
            || field.fieldType == 'int'
            || field.fieldType == 'long'
            || field.fieldType == 'double'
            || field.fieldType == 'decimal'
            || field.fieldType == 'kilometer'
            || field.fieldType == 'meter') {
            width = 100;
          } else {
            width = Math.max(50, Math.min(200, field.size * 6));
          }

          field.width = width;
        }

        let align = 'center';

        if (field.fieldType == 'stake'
          || field.fieldType == 'int'
          || field.fieldType == 'long'
          || field.fieldType == 'double'
          || field.fieldType == 'decimal'
          || field.fieldType == 'kilometer'
          || field.fieldType == 'meter') {
          align = 'right';
        } else if (field.fieldType == 'date'
          || field.fieldType == 'timestamp') {
          align = 'left';
        }

        columnList.push(reactive({
          title: field.title || field.name,
          dataIndex: field.name,
          key: field.name,
          width,
          align,
          sorter: true,
          ellipsis: field.ellipsis !== false,
          resizable: true,
        }));
      }
      columnList.push({
        title: "操作",
        key: "__operationList",
        fixed: "right",
        width: 100,
      });

      return columnList;
    },
    selectedCount() {
      return this.selectedItemListIndicator.selectedItemList.length;
    },
    internalPagination: {
      get() {
        return this.variablePagination;
      },
      set(value) {
        this.variablePagination = value;
      }
    },
    requiredConditionFieldList() {
      let requiredConditionFieldList = [];
      let fieldList = this.fieldList;

      for (let i in fieldList) {
        let field = fieldList[i];

        if (field.showOnCondition == 'required') {
          requiredConditionFieldList.push({ ...field });
        }
      }

      return requiredConditionFieldList;
    },
    internalCriteria: {
      get() {
        return this.variableCriteria;
      },
      set(value) {
        this.variableCriteria = value;
        this.$emit('update:criteriaList', value);
      },
    },
  },
  methods: {
    toggleShowSoftDeleted() {
      this.showSoftDeleted = !this.showSoftDeleted;
      this.selectedItemListIndicator.deselectAll();
      this.internalPagination.current = 1;
      this.updateId++;
    },
    deleteSelectedItem() {
      this.confirm.show(
        this.$daisyStringManager.format("daisy.confirm.deleteSelectedText"),
        this.$daisyStringManager.format("daisy.confirm.deleteSelectedTitle")
      ).then(() => {
        if (this.deleteItemList) {
          this.deleteItemList(this.selectedItemListIndicator.selectedItemList).then(() => {
            this.selectedItemListIndicator.deselectAll();
            this.updateId++;
          });
        }
      });
    },
    deleteItem(item) {
      this.confirm.show(
        this.$daisyStringManager.format("daisy.confirm.deleteItemText"),
        this.$daisyStringManager.format("daisy.confirm.deleteItemTitle")
      ).then(() => {
        if (this.deleteItemList) {
          this.deleteItemList([item]).then(() => {
            this.selectedItemListIndicator.deselect([item]);
            this.updateId++;
          });
        }
      });
    },
    recoverItem(item) {
      this.confirm.show(
        this.$daisyStringManager.format("daisy.confirm.recoverItemText"),
        this.$daisyStringManager.format("daisy.confirm.recoverItemTitle")
      ).then(() => {
        if (this.recoverItemList) {
          this.recoverItemList([item]).then(() => {
            this.selectedItemListIndicator.deselect([item]);
            this.updateId++;
          });
        }
      });
    },
    activate() {
      this.selectedItemListIndicator.deselectAll();
      this.internalPagination.current = 1;
      this.updateId++;
    },
    deactivate() {
      // Keep Alive组件需要实现这个方法，不然重新加载的时候会报异常
    },
    handleChange(pagination, filters, sorter, data) {
      (filters);
      if (data.action == "paginate") {
        this.internalPagination = { ...pagination };
      }
      if (data.action == 'sort') {
        if (!sorter.order) {
          this.sortOrder = [];
        } else {
          if (sorter.order == 'ascend') {
            this.sortOrder = ['+' + sorter.columnKey];
          } else {
            this.sortOrder = ['-' + sorter.columnKey];
          }
        }
        this.internalPagination.current = 1;
      }
    },
    handleResizeColumn(w, col) {
      col.width = w;
    },
    handleSelectionChange(selectedKeyListInPage) {
      this.selectedItemListIndicator.deselect(this.recordList.filter(record => !selectedKeyListInPage.includes(record.key)));
      this.selectedItemListIndicator.select(this.recordList.filter(record => selectedKeyListInPage.includes(record.key)));
    },
    refresh() {
      (this.updateId);
      let pagination = this.internalPagination;
      let criteriaList = [];

      if (typeof (unref(this.$props.criteriaList)) == typeof ([])) {
        criteriaList = [...unref(this.$props.criteriaList)];
      }
      criteriaList = criteriaList.concat(this.queryConditionPanel.criteriaList);
      let args = [
        { currentPage: pagination.current || 1, pageSize: pagination.pageSize || pagination.defaultPageSize },
        criteriaList,
        this.showSoftDeleted,
        this.sortOrder
      ];
      this.loading = true;
      window.setTimeout(() => {
        this.$props.loadData.apply(this, args)
          .then(result => {
            let { recordList, totalRecordCount } = result;
            this.recordList = recordList;
            this.internalPagination.total = totalRecordCount;
          })
          .finally(() => {
            this.loading = false;
          })
      }, 0);
    },
    doOperation(operation, serverRecord) {
      if (!operation.prompt) {
        operation.action.call(this, serverRecord);
        return;
      }

      var prompt = operation.prompt(serverRecord);

      if (!prompt) {
        operation.action.call(this, serverRecord);
        return;
      }

      this.confirm.show(prompt.text, prompt.title).then(() => {
        operation.action.call(this, serverRecord);
      });
    },
    showExportModal() {
      this.tableExportModal.show();
    },
    showFieldChooser() {
      let index = 0;
      let fieldList = this.fieldList
        .filter(field => field.showOnList != 'disable')
        .map(field => {
          let o = { name: field.name, title: field.title, key: index++ };

          if (field.showOnList == 'visible') {
            o.readonly = true;
          }
          if (field.showOnList == 'visible' || this.fieldVisiableStateMap[field.name]) {
            o.visible = true;
          }

          return o;
        });
      this.fieldChooser.show(fieldList).then(fieldList => {
        let fieldVisiableStateMap = {};
        fieldList.forEach(field => {
          if (!field.visible) {
            return;
          }
          fieldVisiableStateMap[field.name] = true;
        });
        this.fieldVisiableStateMap = fieldVisiableStateMap;
      });
    }
  },
  mounted() {
    this.$emit('mount', { table: this });
    // 加载第一页数据
    watchEffect(() => this.refresh());
    watchEffect(() => {
      let selectedItemKeyList = this.selectedItemListIndicator.selectedItemList
        .map(item => item.key);
      this.selectedKeyListInPage =
        this.recordList.filter(
          record => selectedItemKeyList.includes(record.key))
          .map(record => record.key);
    });

    watchEffect(() => {
      var dictionaryList = [];
      for (let index in this.fieldList) {
        let field = this.fieldList[index];
        if (field.dictionary != null) {
          dictionaryList.push(field.dictionary);
        }
      }
      this.$daisyDictionaryManager.load(dictionaryList);
    });
    watchEffect(() => {
      this.queryConditionPanel.fieldList = this.requiredConditionFieldList;
    });
  },
  setup(propList) {
    const loading = ref(false);
    const selectedKeyListInPage = ref([]);

    const tableExportModal = useTableExportModal();
    tableExportModal.exportData = computed(() => propList.exportData);
    tableExportModal.filename = computed(() => propList.entityDisplayName);
    tableExportModal.fieldList = computed(() => propList.fieldList);
    tableExportModal.selectedItemList = computed(() => selectedItemListIndicator.selectedItemList);

    const recordList = ref([]);
    const criteriaList = ref([]);

    const showSoftDeleted = ref(false);

    const confirm = useConfirm();

    const sortOrder = ref([]);

    const fieldChooser = useFieldChooser();
    const fieldVisiableStateMap = ref({});

    const metadata = ref(null);

    const selectedItemListIndicator = useSelectedItemListIndicator();
    selectedItemListIndicator.entityDisplayName = computed(() => propList.entityDisplayName);
    selectedItemListIndicator.fieldList = computed(() => {
      if (propList.fieldList) {
        return propList.fieldList.filter(f => f.showOnSelectedList == 'visible');
      } else {
        return [];
      }
    });

    return reactive({
      metadata,
      updateId: 0,
      queryConditionPanel: useQueryConditionPanel(),
      variableCriteria: typeof (propList.criteriaList) == typeof ([]) ? [...propList.criteriaList] : [],
      variablePagination: {
        defaultPageSize: 10,
        showSizeChanger: true,
        pageSizeOptions: ["10", "20", "50"],
        total: 0,
      },
      tableExportModal,
      selectedItemListIndicator,
      recordList,
      loading,
      selectedKeyListInPage,
      criteriaList,
      showSoftDeleted,
      confirm,
      fieldChooser,
      sortOrder,
      fieldVisiableStateMap,
    });
  },
});
</script>

<style scoped>
.container {
  position: absolute;
  left: 34px;
  right: 31px;
  top: 18px;
  bottom: 26px;
  overflow: hidden;
  display: flex;
  flex-flow: column;
}


.condition-panel {
  flex: 0 1 auto;
}

.toolbar {
  flex: 0 1 auto;
  margin-bottom: 16px;
}

/* 下面的一大段css，是为了让antd的表格控件可以撑满内容区域 */
.table {
  flex: 1 1 auto;
  position: relative;
  overflow: hidden;
}

::v-deep(.table)>.ant-table-wrapper {
  position: absolute;
  inset: 0px;
}

::v-deep(.table)>.ant-table-wrapper>.ant-spin-nested-loading {
  height: 100%;
}

::v-deep(.table)>.ant-table-wrapper>.ant-spin-nested-loading>.ant-spin-container {
  height: 100%;
  display: flex;
  flex-flow: column;
}

::v-deep(.table)>.ant-table-wrapper>.ant-spin-nested-loading>.ant-spin-container>.ant-table {
  overflow: hidden;
  flex: 1 1 auto;
  white-space: nowrap;
}

::v-deep(.table)>.ant-table-wrapper>.ant-spin-nested-loading>.ant-spin-container>.ant-pagination {
  flex: 0 0 auto;
}

::v-deep(.table)>.ant-table-wrapper>.ant-spin-nested-loading>.ant-spin-container>.ant-table>.ant-table-container {
  display: flex;
  flex-flow: column;
  height: 100%;
}

::v-deep(.table)>.ant-table-wrapper>.ant-spin-nested-loading>.ant-spin-container>.ant-table>.ant-table-container>.ant-table-header {
  flex: 0 0 auto;
}

::v-deep(.table)>.ant-table-wrapper>.ant-spin-nested-loading>.ant-spin-container>.ant-table>.ant-table-container>.ant-table-body {
  flex: 1 1 auto;
}
</style>
