<template>
  <div class="container">
    <div class="condition-panel">
      <query-condition-panel :field-list="requiredConditionFieldList" @search="handleSearch" @reset="handleSearch" />
    </div>
    <div class="toolbar">
      <a-space :size="10">
        <a-button type="primary" v-if="!this.showSoftDeleted" @click="this.$emit('createNew', { name: 'createNew' })">
          <plus-outlined />新建
        </a-button>
        <a-button type="primary" v-if="!this.showSoftDeleted" disabled
          @click="this.$emit('createNew', { name: 'createNew' })">
          <import-outlined />导入
        </a-button>
        <a-button danger v-if="!this.showSoftDeleted" :disabled="selectedCount == 0"
          @click="() => this.deleteSelected()">
          <delete-outlined />删除
        </a-button>
        <a-button type="{ this.showSoftDeleted ? 'primary' : '' }" @click="() => this.toggleShowSoftDeleted()">
          <delete-outlined />{{ this.showSoftDeleted ? '返回列表' : '回收站' }}
        </a-button>
        <a-button v-if="!this.showSoftDeleted" @click="showExportModal">
          <export-outlined />导出
        </a-button>
        <a-button @click="showFieldChooser">
          <unordered-list-outlined />选择列
        </a-button>
      </a-space>
    </div>
    <div class="selector" v-if="selectedCount > 0">
      <info-circle-filled :style="{ color: '#1890FF' }" />
      <span style="margin-left: 7px">已选&nbsp;</span><span style="color: #1890FF">{{ selectedCount }}</span><span
        style="margin-right: 63px">&nbsp;项</span>
      <a-button type="link" size="small" :disabled="selectedCount == 0" @click="showSelected">
        查看
      </a-button>
      <a-button type="link" size="small" :disabled="selectedCount == 0" @click="clearSelected">
        清空
      </a-button>
      <a-modal v-model:visible="showSelectedVisible" :bodyStyle="{ height: '587px' }" :title="showSelectedTitle"
        @ok="showSelectedHandleOk">
        <template #footer>
          <div style="text-align: center;">
            <a-button key="delete" :disabled="showSelectedSelectedCount == 0" @click="showSelectedHandleDelete">删除
            </a-button>
            <a-button key="submit" type="primary" @click="showSelectedHandleOk">确定</a-button>
          </div>
        </template>
        <div class="selected-container">
          <div class="selector" v-if="selectedCount > 0">
            <info-circle-filled :style="{ color: '#1890FF' }" />
            <span style="margin-left: 7px">已选{{ title.value }}&nbsp;</span><span style="color: #1890FF">{{
                showSelectedSelectedCount
            }}/{{ selectedCount }}</span><span style="margin-right: 63px">&nbsp;项</span>
          </div>
          <div class="table">``
            <a-table :columns="selectedTableColumnList" :dataSource="selectedRecordList" :pagination="false"
              :row-selection="{
                selectedRowKeys: showSelectedSelectedKeyList,
                onChange: handleSelectedRecordSelectChange,
              }" @change="handleChange" bordered size="small" :scroll="{ x: 400, y: -1 }">
            </a-table>
          </div>
        </div>
      </a-modal>
    </div>
    <div class="table">
      <a-table :columns="tableColumnList" :dataSource="recordList" :pagination="paginationOptions" :loading="loading"
        :row-selection="{
          selectedRowKeys: selectedKeyListInPage,
          onChange: handleSelectChange,
        }" @change="handleChange" @resizeColumn="handleResizeColumn" bordered size="small" :scroll="{ x: -1, y: -1 }">
        <template #bodyCell="{ column, record }">
          <template v-if="column.key === '__operationList'">
            <template v-for="recordOperation in recordOperationList" :key="recordOperation.id">
              <a-button v-if="recordOperation.show(record)" type="link" size="small"
                @click="doOperation(recordOperation, record.__serverRecord)">
                {{ recordOperation.title }}
              </a-button>
            </template>
          </template>
        </template>
      </a-table>
    </div>
  </div>
  <a-confirm v-bind="confirm.propList" />
  <field-chooser v-bind="fieldChooser.propList" />
  <table-export-modal v-if="tableExportModal.propList.visible" v-bind="tableExportModal.propList" />
</template>
<script>
import Confirm, { useConfirm } from "@/components/Confirm.vue";
import { getTransformer } from "@/misc/Transform";
import { DeleteOutlined, ExportOutlined, ImportOutlined, InfoCircleFilled, PlusOutlined, UnorderedListOutlined } from '@ant-design/icons-vue';
import { Button, Modal, Space, Table } from 'ant-design-vue';
import "ant-design-vue/lib/button/style/css";
import "ant-design-vue/lib/modal/style/css";
import "ant-design-vue/lib/space/style/css";
import "ant-design-vue/lib/table/style/css";
import { computed, defineComponent, reactive, ref, unref, watch } from "vue";
import QueryConditionPanel from "./QueryConditionPanel.vue";
import TableExportModal, { useTableExportModal } from "./TableExportModal.vue";
import FieldChooser, { useFieldChooser } from "@/components/crud/FieldChooser.vue";

export default defineComponent({
  name: "DaisyCRUDTable",
  components: {
    PlusOutlined,
    ImportOutlined,
    ExportOutlined,
    DeleteOutlined,
    InfoCircleFilled,
    UnorderedListOutlined,
    'AConfirm': Confirm,
    'QueryConditionPanel': QueryConditionPanel,
    'FieldChooser': FieldChooser,
    'TableExportModal': TableExportModal,
    'ATable': Table,
    'AButton': Button,
    'AModal': Modal,
    'ASpace': Space
  },
  emits: ["mount", "createNew", "edit", 'deleteSelected'],
  props: {
    title: {
      type: String,
      required: true,
    },
    tableRef: {
      type: Object,
      required: true,
    },
    recordOperationList: {
      type: Object,
      required: true,
    },
    entityDataManager: {
      type: Object,
      required: true,
    },
    tableLoadData: {
      type: Function,
      required: true,
    },
    tableLoadCriteria: {
      type: Array,
    },
    deleteRecordList: {
      type: Function,
    }
  },
  computed: {
    tableColumnList() {
      const columnList = [];
      for (var index = 0; index < unref(this.fieldList).length; index++) {
        let field = unref(this.fieldList)[index];
        if (field.showOnList == "disable") {
          continue;
        }
        if (field.showOnList == "hidden" && !this.fieldVisiableStateMap[field.name]) {
          continue;
        }

        let width = field.width;

        if (!width || width == 0) {
          if (isNaN(field.size)) {
            width = 100;
          } else if (field.fieldType == 'stake'
            || field.fieldType == 'int'
            || field.fieldType == 'long'
            || field.fieldType == 'double'
            || field.fieldType == 'decimal'
            || field.fieldType == 'kilometer'
            || field.fieldType == 'meter') {
            width = 100;
          } else {
            width = Math.max(50, Math.min(200, field.size * 6));
          }
        }

        let align = 'center';

        if (field.fieldType == 'stake'
          || field.fieldType == 'int'
          || field.fieldType == 'long'
          || field.fieldType == 'double'
          || field.fieldType == 'decimal'
          || field.fieldType == 'kilometer'
          || field.fieldType == 'meter') {
          align = 'right';
        } else if (field.fieldType == 'date'
          || field.fieldType == 'timestamp') {
          align = 'left';
        }

        columnList.push(reactive({
          title: field.title || field.name,
          dataIndex: field.name,
          key: field.name,
          width,
          align,
          sorter: true,
          ellipsis: field.ellipsis !== false,
          resizable: true,
        }));
      }
      columnList.push({
        title: "操作",
        key: "__operationList",
        fixed: "right",
        width: 100,
      });

      return columnList;
    },
    selectedTableColumnList() {
      const columnList = [];
      for (var index = 0; index < unref(this.fieldList).length; index++) {
        let field = unref(this.fieldList)[index];
        if (field.showOnSelectedList != "visible") {
          continue;
        }
        columnList.push({
          title: field.title || field.name,
          dataIndex: field.name,
          key: field.name,
          width: field.width || 100,
          ellipsis: field.ellipsis !== false,
        });
      }

      return columnList;
    },
    fieldList() {
      return unref(this.entityDataManager.metadata)?.fieldList || [];
    },
    selectedCount() {
      return this.selectedRecordList.length;
    },
    showSelectedSelectedCount() {
      return this.showSelectedSelectedKeyList.length;
    },
    requiredConditionFieldList() {
      let requiredConditionFieldList = [];
      let fieldList = this.fieldList;

      for (let i in fieldList) {
        let field = fieldList[i];

        if (field.showOnCondition == 'required') {
          requiredConditionFieldList.push({ ...field });
        }
      }

      return requiredConditionFieldList;
    },
  },
  methods: {
    toggleShowSoftDeleted() {
      this.showSoftDeleted = !this.showSoftDeleted;
      this.clearSelected();
      this.refresh();
    },
    deleteSelected() {
      this.confirm.show(
        this.$daisyStringManager.format("daisy.confirm.deleteSelectedText"),
        this.$daisyStringManager.format("daisy.confirm.deleteSelectedTitle")
      ).then(() => {
        if (this.deleteRecordList) {
          this.deleteRecordList(this.selectedRecordList).then(() => {
            this.clearSelected();
            this.refresh();
          });
        }
      });
    },
    activate() {
      this.refresh();
    },
    deactivate() {
      // Keep Alive组件需要实现这个方法，不然重新加载的时候会报异常
    },
    syncSelectedKeys() {
      var selectedKeyListInPage = [];
      for (let index in this.recordList) {
        let record = this.recordList[index];
        if (this.selectedRecordMap[record.key]) {
          selectedKeyListInPage.push(record.key);
        }
      }
      this.selectedKeyListInPage = selectedKeyListInPage;
    },
    handleSearch(e) {
      this.criteriaList = e.criteriaList;
      this.refresh();
    },
    handleChange(pagination, filters, sorter, data) {
      (filters);
      if (data.action == "paginate") {
        this.refresh(pagination);
      }
      if (!sorter.order) {
        this.sortOrder = [];
      } else {
        if (sorter.order == 'ascend') {
          this.sortOrder = ['+' + sorter.columnKey];
        } else {
          this.sortOrder = ['-' + sorter.columnKey];
        }
      }
      if (data.action == 'sort') {
        // TODO: 这里没有重置列表到首页
        // this.pagination.current = 1;
        this.refresh();
      }
    },
    handleResizeColumn(w, col) {
      col.width = w;
    },
    clearSelected() {
      this.selectedRecordMap = [];
      this.selectedRecordList = [];
      this.selectedKeyListInPage = [];
    },
    showSelected() {
      this.showSelectedVisible = true;
    },
    showSelectedHandleOk() {
      this.syncSelectedKeys();
      this.showSelectedVisible = false;
    },
    showSelectedHandleDelete() {
      var selectedRecordList = [];
      var showSelectedSelectedKeyMap = {};

      for (var index = 0; index < this.showSelectedSelectedKeyList.length; index++) {
        let key = this.showSelectedSelectedKeyList[index];
        showSelectedSelectedKeyMap[key] = true;
      }
      for (var key in this.selectedRecordMap) {
        if (showSelectedSelectedKeyMap[key]) {
          delete this.selectedRecordMap[key];
          continue;
        }
        selectedRecordList.push(this.selectedRecordMap[key]);
      }
      this.selectedRecordList = selectedRecordList;
      this.showSelectedSelectedKeyList = []
      this.syncSelectedKeys();
    },
    handleSelectedRecordSelectChange(selectedKeyList) {
      this.showSelectedSelectedKeyList = selectedKeyList;
    },
    handleSelectChange(selectedKeyListInPage) {
      var selectedRecordMap = {};
      var recordMapInPage = {};
      var selectedRecordList = [];
      for (let index in this.recordList) {
        let record = this.recordList[index];
        recordMapInPage[record.key] = record;
      }
      for (let key in this.selectedRecordMap) {
        if (recordMapInPage[key]) {
          continue;
        }
        selectedRecordMap[key] = this.selectedRecordMap[key];
        selectedRecordList.push(this.selectedRecordMap[key]);
      }
      for (let index in selectedKeyListInPage) {
        let key = selectedKeyListInPage[index];
        selectedRecordMap[key] = recordMapInPage[key];
        selectedRecordList.push(recordMapInPage[key]);
      }
      this.selectedRecordMap = selectedRecordMap;
      this.selectedRecordList = selectedRecordList;
      this.selectedKeyListInPage = [...selectedKeyListInPage];
    },
    refresh(pagination) {
      console.log('refresh');
      this.loading = true;
      pagination = pagination || this.pagination;
      let tableLoadCriteria = [];

      if (typeof (this.tableLoadCriteria) == typeof ([])) {
        tableLoadCriteria = [...unref(this.tableLoadCriteria)];
      }
      tableLoadCriteria = tableLoadCriteria.concat(this.criteriaList);
      this.tableLoadData({ currentPage: pagination.current || 1, pageSize: pagination.pageSize || this.paginationOptions.defaultPageSize }, tableLoadCriteria, this.showSoftDeleted, this.sortOrder)
        .then((data) => {
          var tableRecordList = [];

          for (let recordIndex = 0; recordIndex < data.recordList.length; recordIndex++) {
            var serverRecord = data.recordList[recordIndex];
            var tableRecord = { key: serverRecord.id };

            for (let index = 0; index < unref(this.fieldList).length; index++) {
              let field = unref(this.fieldList)[index];
              let transformer = getTransformer(field.transformer) || getTransformer(field.fieldType + 'InCRUDTable') || getTransformer('noop');

              if (field.dictionary) {
                tableRecord[field.name] = transformer.toDisplay(this.$daisyDictionaryManager.getTitle(field.dictionary, serverRecord[field.name]) || '');
              } else {
                tableRecord[field.name] = transformer.toDisplay(serverRecord[field.name]) || '';
              }
            }
            tableRecord.__serverRecord = serverRecord;
            tableRecordList.push(tableRecord);
          }
          this.recordList = tableRecordList;
          this.paginationOptions = {
            ...this.paginationOptions,
            total: data.total,
          };
          this.syncSelectedKeys();
        })
        .finally(() => (this.loading = false));
    },
    doOperation(operation, serverRecord) {
      if (!operation.prompt) {
        operation.action.call(this, serverRecord);
        return;
      }

      var prompt = operation.prompt(serverRecord);

      if (!prompt) {
        operation.action.call(this, serverRecord);
        return;
      }

      this.confirm.show(prompt.text, prompt.title).then(() => {
        operation.action.call(this, serverRecord);
      });
    },
    showExportModal() {
      this.tableExportModal.show();
    },
    showFieldChooser() {
      let index = 0;
      let fieldList = this.fieldList
        .filter(field => field.showOnList != 'disable')
        .map(field => {
          let o = { name: field.name, title: field.title, key: index++ };

          if (field.showOnList == 'visible') {
            o.readonly = true;
          }
          if (field.showOnList == 'visible' || this.fieldVisiableStateMap[field.name]) {
            o.visible = true;
          }

          return o;
        });
      this.fieldChooser.show(fieldList).then(fieldList => {
        let fieldVisiableStateMap = {};
        fieldList.forEach(field => {
          if (!field.visible) {
            return;
          }
          fieldVisiableStateMap[field.name] = true;
        });
        this.fieldVisiableStateMap = fieldVisiableStateMap;
      });
    }
  },
  mounted() {
    // 加载第一页数据
    this.refresh();
    if (typeof (this.tableLoadCriteria) == typeof ([])) {
      watch(() => { this.tableLoadCriteria }, () => this.refresh());
    }
    watch(() => { unref(this.sortOrder) }, () => this.refresh());
    this.$emit("mount", { "name": "mount", "component": this });
  },
  setup(propList) {
    const loading = ref(false);
    const paginationOptions = {
      defaultPageSize: 10,
      showSizeChanger: true,
      pageSizeOptions: ["10", "20", "50"],
      total: 0,
    };
    const selectedRecordMap = ref({});
    const selectedKeyListInPage = ref([]);
    const showSelectedTitle = computed(() => '已选择' + unref(propList.title));
    const showSelectedVisible = ref(false);
    const selectedRecordList = ref([]);
    const showSelectedSelectedKeyList = ref([]);
    const pagination = ref({});

    const recordList = ref([]);
    const criteriaList = ref([]);

    const showSoftDeleted = ref(false);

    const confirm = useConfirm();

    const sortOrder = ref([]);

    const fieldChooser = useFieldChooser();
    const fieldVisiableStateMap = ref({});

    function exportData() {
      return propList.entityDataManager.exportData.apply(propList.entityDataManager, Array.prototype.slice.call(arguments))
        .then(response => {
          return response.data.data.downloadUrl;
        });
    }

    const tableExportModal = useTableExportModal();
    tableExportModal.exportData = computed(() => exportData);
    tableExportModal.filename = computed(() => unref(propList.entityDataManager.metadata)?.title);
    tableExportModal.fieldList = computed(() => unref(propList.entityDataManager.metadata)?.fieldList || []);
    tableExportModal.selectedItemList = computed(() => selectedRecordList.value);

    return reactive({
      recordList,
      showSelectedTitle,
      showSelectedVisible,
      paginationOptions,
      pagination,
      loading,
      selectedRecordMap,
      selectedRecordList,
      showSelectedSelectedKeyList,
      selectedKeyListInPage,
      criteriaList,
      showSoftDeleted,
      confirm,
      fieldChooser,
      sortOrder,
      fieldVisiableStateMap,
      tableExportModal,
    });
  },
});
</script>

<style scoped>
.container {
  position: absolute;
  left: 34px;
  right: 31px;
  top: 18px;
  bottom: 26px;
  overflow: hidden;
  display: flex;
  flex-flow: column;
}

.selected-container {
  position: relative;
  height: 100%;
  overflow: hidden;
  display: flex;
  flex-flow: column;
}

.condition-panel {
  flex: 0 1 auto;
}

.toolbar {
  flex: 0 1 auto;
  margin-bottom: 16px;
}

.selector {
  flex: 0 1 auto;
  margin-bottom: 12px;
  border: 1px solid #91D5FF;
  background-color: #E6F7FF;
  padding: 12px 16px;
}

.table {
  flex: 1 1 auto;
  position: relative;
  overflow: hidden;
}

/* 下面的一大段css，是为了让antd的表格控件可以撑满内容区域 */
::v-deep(.table)>.ant-table-wrapper {
  position: absolute;
  inset: 0px;
}

::v-deep(.table)>.ant-table-wrapper>.ant-spin-nested-loading {
  height: 100%;
}

::v-deep(.table)>.ant-table-wrapper>.ant-spin-nested-loading>.ant-spin-container {
  height: 100%;
  display: flex;
  flex-flow: column;
}

::v-deep(.table)>.ant-table-wrapper>.ant-spin-nested-loading>.ant-spin-container>.ant-table {
  overflow: hidden;
  flex: 1 1 auto;
  white-space: nowrap;
}

::v-deep(.table)>.ant-table-wrapper>.ant-spin-nested-loading>.ant-spin-container>.ant-pagination {
  flex: 0 0 auto;
}

::v-deep(.table)>.ant-table-wrapper>.ant-spin-nested-loading>.ant-spin-container>.ant-table>.ant-table-container {
  display: flex;
  flex-flow: column;
  height: 100%;
}

::v-deep(.table)>.ant-table-wrapper>.ant-spin-nested-loading>.ant-spin-container>.ant-table>.ant-table-container>.ant-table-header {
  flex: 0 0 auto;
}

::v-deep(.table)>.ant-table-wrapper>.ant-spin-nested-loading>.ant-spin-container>.ant-table>.ant-table-container>.ant-table-body {
  flex: 1 1 auto;
}
</style>
