<template>
  <div>
    <a-modal v-model:visible="internalVisible" :style="{ width: '631px' }" :bodyStyle="{ height: '583px' }" title="导出数据"
      @ok="handleOk">
      <template #footer>
        <div style="text-align: center;">
          <a-button key="ok" :disabled="!submitEnabled" @click="handleOk">确定</a-button>
          <a-button key="cancel" type="primary" @click="handleCancel">取消</a-button>
        </div>
      </template>
      <div class="content-container" style="padding: 10px 20px">
        <a-row :style="{ 'padding': '15px 22px', 'align-items': 'center' }">
          <a-col span="6" style="padding-right: 10px" align="end">文件名</a-col>
          <a-col span="18">
            <a-input v-model:value="exportFilename"></a-input>
          </a-col>
        </a-row>
        <a-row :style="{ 'padding': '15px 22px', 'align-items': 'center' }">
          <a-col span="6" style="padding-right: 10px" align="end">导出格式</a-col>
          <a-col span="18">
            <a-select v-model:value="exportFormat" style="width: 100%">
              <a-select-option value="excel">Excel(*.xlsx)</a-select-option>
              <!-- <a-select-option value="csv">CSV(逗号分割)(*.csv)</a-select-option> -->
            </a-select>
          </a-col>
        </a-row>
        <a-row :style="{ 'padding': '15px 22px', 'align-items': 'center' }">
          <a-col span="6" style="padding-right: 10px" :align="'end'">选择数据</a-col>
          <a-col span="18">
            <a-select v-model:value="exportType" style="width: 100%">
              <a-select-option value="selected">当前数据当前页</a-select-option>
              <a-select-option value="all">所有数据</a-select-option>
            </a-select>
          </a-col>
        </a-row>
        <a-row :style="{ 'padding': '15px 22px', 'align-items': 'center' }">
          <a-col span="6" style="padding-right: 10px" :align="'end'">选择字段</a-col>
          <a-col span="18">
            <a-table :columns="fieldTableColumnList" :dataSource="fieldTableRowList" :pagination="false" :row-selection="{
              selectedRowKeys: fieldTableSelectedRowKeys,
              onChange: handleFieldTableSelectionChange,
            }" bordered size="small" :scroll="{ x: -1, y: 280 }">
            </a-table>
          </a-col>
        </a-row>
      </div>
    </a-modal>
  </div>
  <div style="width: 0px; height: 0px; overflow: hidden" v-if="downloadUrl">
    <iframe :ref='downloadFrame' @load="handleLoad" :src="downloadUrl"></iframe>
  </div>
</template>
<script>
import { computed } from '@vue/reactivity';
import { Button, Col, Input, Modal, Row, Select, SelectOption, Table } from 'ant-design-vue';
import "ant-design-vue/lib/button/style/css";
import "ant-design-vue/lib/col/style/css";
import "ant-design-vue/lib/input/style/css";
import "ant-design-vue/lib/modal/style/css";
import "ant-design-vue/lib/row/style/css";
import "ant-design-vue/lib/select/style/css";
import "ant-design-vue/lib/table/style/css";
import { defineComponent, reactive } from "vue";

export function useTableExportModal() {
  let resolver = null;

  function show() {
    context.visible = true;
    return new Promise(r => {
      resolver = r;
    });
  }

  let context = reactive({
    propList: {
      visible: computed(() => context.visible),
      'onUpdate:visible': (value) => {
        if (!value && resolver) {
          resolver();
          resolver = null;
        }
        context.visible = value;
        return;
      },
      filename: computed(() => context.filename),
      fieldList: computed(() => context.fieldList),
      selectedItemList: computed(() => context.selectedItemList || []),
      exportData: computed(() => context.exportData),
    },
    visible: false,
    filename: '',
    fieldList: null,
    selectedItemList: null,
    exportData: null,
    show,
  });

  return context;
}

export default defineComponent({
  name: "TableExportMadal",
  props: {
    visible: {
      type: Boolean,
      required: true,
    },
    filename: {
      type: String,
    },
    fieldList: {
      type: Array,
      required: true,
    },
    selectedItemList: {
      type: Array,
      required: true,
    },
    exportData: {
      type: Function,
      required: true,
    },
  },
  emits: ['update:visible'],
  components: {
    "ATable": Table,
    "AButton": Button,
    "AModal": Modal,
    "ASelect": Select,
    "ASelectOption": SelectOption,
    "AInput": Input,
    "ARow": Row,
    "ACol": Col,
  },
  computed: {
    internalVisible: {
      get() {
        return this.$props.visible;
      },
      set(value) {
        this.$emit('update:visible', value);
      }
    },
    fieldTableColumnList() {
      return [{
        title: '全部字段',
        dataIndex: 'title',
        width: 200,
      }];
    },
    fieldTableRowList() {
      var rowList = [];

      for (var i in this.fieldList) {
        var field = this.fieldList[i];
        if (field.showOnList == 'disable') {
          continue;
        }

        var row = {
          key: i,
          name: field.name,
          title: field.title,
        };
        rowList.push(row);
      }

      return rowList;
    },
    submitEnabled() {
      if (this.fieldTableSelectedRowKeys.length == 0) {
        return false;
      }
      if (this.exportType == 'selected' && this.selectedItemList.length == 0) {
        return false;
      }
      if (!this.exportFilename) {
        return false;
      }
      if (this.submitting) {
        return false;
      }
      return true;
    }
  },
  methods: {
    handleLoad() {
      console.log('load');
    },
    handleOk() {
      let selectedIdList = null;

      if (this.exportType == 'selected') {
        selectedIdList = [];

        for (let i in this.selectedItemList) {
          selectedIdList.push(this.selectedItemList[i].id);
        }
      }

      let selectedFieldList = [];
      for (let i in this.fieldTableSelectedRowKeys) {
        selectedFieldList.push(this.fieldList[this.fieldTableSelectedRowKeys[i]].name);
      }
      this.submitting = true;
      this.$props.exportData(this.exportFilename, this.exportFormat, selectedIdList, selectedFieldList)
        .then((downloadUrl) => {
          this.downloadUrl = downloadUrl;
        })
        .finally(() => {
          // TODO: 错误处理
          this.submitting = false;
        });
    },
    handleCancel() {
      this.internalVisible = false;
    },
    handleFieldTableSelectionChange(selectedRowKeys) {
      this.fieldTableSelectedRowKeys = selectedRowKeys
    },
  },
  setup(propList) {
    let fieldTableSelectedRowKeys = [];
    let p = propList;

    for (let i in p.fieldList) {
      let field = p.fieldList[i];
      if (field.showOnList == 'visible') {
        fieldTableSelectedRowKeys.push(i);
      }
    }

    return reactive({
      exportFilename: (p.filename || '未命名') + '.xlsx',
      exportFormat: 'excel',
      exportType: 'all',
      submitting: false,
      fieldTableSelectedRowKeys,
      downloadUrl: null,
      downloadFrame: null,
    });
  }
});
</script>
