<script>
import { defineComponent, unref, ref } from "vue";
import { Input, InputGroup } from 'ant-design-vue';
import { getTransformer } from "../../misc/Transform";

import "ant-design-vue/lib/input/style/css";


// { format: ['11', () => this.record['jgjb'], ref(''), () => this.record['sszq']] }

export default defineComponent({
    name: 'CompositeSerialEditor',
    props: {
        record: {
            type: Object,
            required: true,
        },
        column: {
            type: Object,
            required: true,
        },
    },
    components: { 'AInput': Input, 'AInputGroup': InputGroup },
    deactivated() {
        this.transientValue = null;
    },
    computed: {
        transformer() {
            var transformer = this.column.transformer;

            if (typeof (transformer) == typeof ("")) {
                transformer = getTransformer(transformer);
            }
            return transformer;
        },
        value: {
            get() {
                if (this.transientValue != null) {
                    return this.transientValue;
                }
                if (this.column.transformer) {
                    return (getTransformer(this.column.transformer) || getTransformer('noop')).toEdit(this.record[this.column.field]);
                } else {
                    return this.record[this.column.field];
                }
            },
            set(newValue) {
                var r = this.record;
                if (this.transformer) {
                    this.transientValue = newValue;
                    try {
                        r[this.column.field] = this.transformer.toStorage(newValue);
                    } catch (e) {
                        console.log('error: ', e);
                    }
                } else {
                    r[this.column.field] = newValue;
                }
                this.$emit('columnChange', { column: this.column })
            }
        },
        caculatedValue() {
            var s = '';
            for (var index = 0; index < this.definitionList.length; index++) {
                s += this.definitionList[index].value();
            }
            return s;
        },
        definitionList() {
            var _this = this;

            if (!this.caculatedDefinitionList) {
                _this.caculatedDefinitionList = [];
                var recordValue = this.record[this.column.field];
                var pendingRecaculate = false;

                for (let i in this.column.params) {
                    var item = this.column.params[i];
                    var definition = ((item) => {
                        var definition = { width: item.width };

                        if (item.type == 'static') {
                            definition.value = () => item.value;
                        } else if (item.type == 'field') {
                            definition.value = () => this.record[item.field];
                        } else if (item.type == 'input') {
                            definition.length = item.length;
                            definition.valueRef = ref(recordValue ? recordValue.substring(item.start + 1, item.start + 1 + item.length) : item.value);
                            definition.value = () => definition.valueRef.value;
                            definition.valueUpdater = (v) => {
                                definition.valueRef.value = v;
                                this.value = this.caculatedValue;
                            };
                            if (!this.record.value && item.value) {
                                pendingRecaculate = true;
                            }
                        }
                        return definition;
                    })(item);
                    _this.caculatedDefinitionList.push(definition);
                }

                let st = setTimeout;

                if (pendingRecaculate) {
                    st(() => _this.value = this.caculatedValue, 0);
                }
            }

            return this.caculatedDefinitionList;
        }
    },
    methods: {
    },
    data() {
        const transientValue = ref();
        const caculatedDefinitionList = null;

        return {
            transientValue,
            caculatedDefinitionList,
        }
    },
    render() {
        var componentList = [];
        var first = true;

        for (let i in this.definitionList) {
            let item = this.definitionList[i];
            let style;

            if (first) {
                style = `width: ${item.width + 10}px; padding-left: 10px; padding-right: 0px;`;
                first = false;
            } else {
                style = `width: ${item.width}px; padding-left: 0px; padding-right: 0px;`;
            }

            if (item.valueUpdater) {
                componentList.push(<a-input value={unref(item.value(this.record))} maxlength={item.length} onUpdate:value={(v) => item.valueUpdater(v)} style={style} />);
            } else {
                componentList.push(<a-input readonly={true} bordered={false} value={unref(item.value(this.record))} style={style} />);
            }
        }
        return <div style="white-space: nowrap"><a-input-group compact>{componentList}</a-input-group></div>;
    },
});
</script>
