export default (record, columns, args, tip) => {
  const exp = args[0];
  if (!exp) {
    // TODO: throw exception
    console.log('regexp validator no pattern')
  }
  const pattern = new RegExp(exp);

  const message = {
    recordMessage: '',
    columnMessage: []
  }

  for (let column of columns) {
    const val = record[column] || '';
    if (!(val.match(pattern))) {
      message.columnMessage.push({
        column: column,
        message: tip || '格式错误'
      })
    }
  }
  return message;
}
