<template>
    <a-row :style="{ alignItems: 'center' }">
        <a-col :span="9" :style="{ paddingLeft: '8px', paddingRight: '8px' }">
            <a-typography
                :style="{ whiteSpace: 'nowrap', overflow: 'hidden', textOverflow: 'ellipsis', textAlign: 'right' }"
                :title="title">{{ title }}</a-typography>
        </a-col>
        <a-col :span="15">
            <a-select v-model:value="value" style="width: 100%" :allowClear="true" :options="optionList" />
            <div v-if="field.validateMessage" style="height: 0; overflow: show;">
                <span style="font-size: 90%; color: red;">{{ field.validateMessage }}</span>
            </div>
        </a-col>
    </a-row>
</template>
<script>
import { Col, Select, Row, Typography } from 'ant-design-vue';
import "ant-design-vue/lib/col/style/css";
import "ant-design-vue/lib/select/style/css";
import "ant-design-vue/lib/row/style/css";
import "ant-design-vue/lib/typography/style/css";
import { defineComponent, ref } from "vue";
import { useDictionaryManager } from '@/misc/DictionaryManager';

export default defineComponent({
    name: "DictionaryFieldEditor",
    props: {
        field: {
            type: Object,
            required: true,
        },
        record: {
            type: Object,
            required: true,
        },
    },
    emits: ['fieldChange'],
    components: {
        "ATypography": Typography,
        "ASelect": Select,
        "ARow": Row,
        "ACol": Col,
    },
    computed: {
        title() {
            return (this.field.required ? '* ' : '') + this.field.title;
        },
        value: {
            get() {
                return this.record[this.field.name];
            },
            set(value) {
                let record = this.record;
                record[this.field.name] = value;
                this.$emit("fieldChange", { field: this.field });
            }
        }
    },
    setup(props) {
        let dictionaryManager = useDictionaryManager();
        let optionList = ref([]);

        dictionaryManager.load([props.field.dictionary]).then(() => {
            optionList.value = dictionaryManager.getSelectOptionList(props.field.dictionary);
        })

        return {
            optionList,
        };
    },
});
</script>