<template>
    <a-row :style="{ alignItems: 'center' }">
        <a-col :span="3" :style="{ paddingLeft: '8px', paddingRight: '8px' }">
            <a-typography
                :style="{ whiteSpace: 'nowrap', overflow: 'hidden', textOverflow: 'ellipsis', textAlign: 'right' }"
                :title="title">{{ title }}</a-typography>
        </a-col>
        <a-col :span="21">
            <a-upload v-model:file-list="fileList" name="file" list-type="picture-card" :action="action"
                :headers="headerList" @change="handleChange" @preview="handlePreview">
                <div v-if="fileList.length < 8">
                    <plus-outlined />
                    <div style="margin-top: 8px">Upload</div>
                </div>
            </a-upload>
            <a-modal :visible="previewVisible" :title="previewTitle" :footer="null" @cancel="handleCancel">
                <img alt="example" style="width: 100%" :src="previewImage" />
            </a-modal>
            <div v-if="field.validateMessage" style="height: 0; overflow: show;">
                <span style="font-size: 90%; color: red;">{{ field.validateMessage }}</span>
            </div>
        </a-col>
    </a-row>
</template>
<script>
import { AUTH_HEADER_PROVIDER, IMAGE_ACTION_PROVIDER, IMAGE_SHOW_PROVIDER } from "@/misc/Symbol";
import { PlusOutlined } from '@ant-design/icons-vue';
import { Col, Modal, Row, Typography, Upload } from 'ant-design-vue';
import "ant-design-vue/lib/col/style/css";
import "ant-design-vue/lib/modal/style/css";
import "ant-design-vue/lib/row/style/css";
import "ant-design-vue/lib/typography/style/css";
import "ant-design-vue/lib/upload/style/css";
import { defineComponent, inject, reactive, ref, computed } from "vue";

function getBase64(file) {
    return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = () => resolve(reader.result);
        reader.onerror = error => reject(error);
    });
}

export default defineComponent({
    name: "ImageFieldEditor",
    props: {
        field: {
            type: Object,
            required: true,
        },
        record: {
            type: Object,
            required: true,
        },
    },
    emits: ['fieldChange'],
    components: {
        "ATypography": Typography,
        "AUpload": Upload,
        "AModal": Modal,
        PlusOutlined,
        "ARow": Row,
        "ACol": Col,
    },
    computed: {
        title() {
            return (this.field.required ? '* ' : '') + this.field.title;
        },
        value: {
            get() {
                return this.record[this.field.name];
            },
            set(value) {
                let record = this.record;
                record[this.field.name] = value;
                this.$emit("fieldChange", { field: this.field });
            }
        }
    },
    methods: {
        handleChange(info) {
            if (info.file.status === 'done' || info.file.status === 'removed') {
                this.storageId = info.file.response.data.storageId;
                this.value = JSON.stringify(info.fileList.map(file => file.response.data));
            }
        },
        handleCancel() {
            this.previewVisible = false;
            this.previewTitle = '';
        },
        async handlePreview(file) {
            if (!file.url && !file.preview) {
                file.preview = await getBase64(file.originFileObj);
            }
            this.previewImage = file.preview || this.imageShowProvider(file.response.data);
            this.previewVisible = true;
            this.previewTitle = file.response.data.originalFilename;
        },
    },
    setup(propMap) {
        const previewVisible = ref(false);
        const previewImage = ref('');
        const previewTitle = ref('');
        const storageId = ref('');
        const authHeaderProvider = inject(AUTH_HEADER_PROVIDER);
        const imageActionProvider = inject(IMAGE_ACTION_PROVIDER);
        const imageShowProvider = inject(IMAGE_SHOW_PROVIDER);
        const fileList = ref([]);

        if (propMap.record[propMap.field.name]) {
            fileList.value = JSON.parse(propMap.record[propMap.field.name]).map(image => {
                return {
                    uid: image.filenameId,
                    name: image.originalFilename,
                    status: 'done',
                    url: imageShowProvider(image, 'preview'),
                    response: {
                        data: image
                    }
                };
            });
        }

        return reactive({
            previewVisible,
            previewImage,
            previewTitle,
            fileList,
            storageId,
            imageShowProvider,
            headerList: ref(authHeaderProvider ? authHeaderProvider() : null),
            action: computed(() => imageActionProvider(propMap.field.editor.params.scenario, storageId.value)),
        });
    },
});
</script>