<template>
  <a-row :style="{ alignItems: 'center' }">
    <a-col :span="9" :style="{ paddingLeft: '8px', paddingRight: '8px' }">
      <a-typography :style="{ whiteSpace: 'nowrap', overflow: 'hidden', textOverflow: 'ellipsis', textAlign: 'right' }"
        :title="title">{{ title }}</a-typography>
    </a-col>
    <a-col :span="15">
      <a-select v-if="!field.getReadonly($props.dataContext)" v-model:value="value" style="width: 100%" :allowClear="true"
        :options="optionList" />
      <a-input v-if="field.getReadonly($props.dataContext)" disabled v-model:value="readonlyValue" />
      <div v-if="validateMessageList && validateMessageList.length > 0" style="height: 0; overflow: show;">
        <div v-for="validateMessage in validateMessageList" v-bind:key="validateMessage"
          style="font-size: 90%; color: red;">{{ validateMessage }}</div>
      </div>
    </a-col>
  </a-row>
</template>
<script>
import { Col, Select, Row, Typography, Input as AInput } from 'ant-design-vue';
import "ant-design-vue/lib/col/style/css";
import "ant-design-vue/lib/select/style/css";
import "ant-design-vue/lib/input/style/css";
import "ant-design-vue/lib/row/style/css";
import "ant-design-vue/lib/typography/style/css";
import { defineComponent, ref, reactive, watchEffect } from "vue";
import { useDictionaryManager } from '@/misc/DictionaryManager';

export function getNamedValue(dataContext, field, name) {
  if (name == '已选选项' || name == 'selectedOption') {
    return field.getByFullPath(dataContext, ['intermediate', field.path, '$.editor.selectedItem'])[0];
  } else if (name == '已选选项值' || name == 'selectedOptionValue') {
    return field.getByFullPath(dataContext, ['intermediate', field.path, '$.editor.selectedItem.value'])[0];
  } else if (name == '已选选项标题' || name == 'selectedOptionTitle') {
    return field.getByFullPath(dataContext, ['intermediate', field.path, '$.editor.selectedItem.title'])[0];
  }
}

export function setNamedValue(dataContext, field, name, value) {
  if (name == '已选选项' || name == 'selectedOption') {
    field.setByFullPath(dataContext, ['intermediate', field.path, '$.editor.selectedItem'], value);
  }
}

export default defineComponent({
  name: "KDictionaryFieldEditor",
  props: {
    field: {
      type: Object,
      required: true,
    },
    fieldList: {
      type: Array,
      required: true,
    },
    dataContext: {
      type: Object,
      required: true,
    },
  },
  emits: ['fieldChange'],
  components: {
    "ATypography": Typography,
    "ASelect": Select,
    "ARow": Row,
    "ACol": Col,
    AInput,
  },
  computed: {
    title() {
      return (this.field.mandatory ? '* ' : '') + this.field.title;
    },
    runtimeContext() {
      return this.$props.dataContext.getRuntimeContext(this.$props.fieldList);
    },
    runtimeContextOfField() {
      return this.runtimeContext.field[this.$props.field.name];
    },
    value: {
      get() {
        return this.runtimeContextOfField['值'];
      },
      set(value) {
        if (typeof (value) == 'undefined') {
          value = null;
        }
        this.runtimeContextOfField['值'] = value;
      }
    },
    readonlyValue() {
      if (this.value == null || typeof (this.value) == 'undefined') {
        return '';
      }

      return this.runtimeContextOfField['已选选项值'] + ' - ' + this.runtimeContextOfField['已选选项标题'];
    },
    validateMessageList() {
      let validateResultList = this.runtimeContextOfField['校验结果'] || [];

      return validateResultList.map(validateResult => validateResult.message);
    },
  },
  mounted() {
    watchEffect(() => {
      this.runtimeContextOfField['已选选项'] = this.dictionaryItemMap[this.value];
    });
  },
  setup(propList) {
    let dictionaryManager = useDictionaryManager();
    let optionList = ref([]);
    let dictionaryItemMap = ref([]);

    dictionaryManager.load([propList.field.editorParameter.dictionary]).then(() => {
      dictionaryItemMap.value = dictionaryManager.getDictionary(propList.field.editorParameter.dictionary);
      optionList.value = [];

      for (let value in dictionaryItemMap.value || []) {
        optionList.value.push({ value, 'label': dictionaryItemMap.value[value].value + ' - ' + dictionaryItemMap.value[value].title });
      }
    })

    return reactive({
      optionList,
      dictionaryItemMap,
    });
  },
});
</script>
