import format from 'string-format';
import { STRING_MANAGER } from '@/misc/Symbol';
import { inject } from 'vue';

class StringManager {
    constructor(request) {
        this.request = request;
        this.loadedStringMap = {
            'daisy.crud.closeConfirm': '确认关闭',
            "daisy.confirm.ok": "确定",
            "daisy.confirm.cancel": "取消",
            "daisy.confirm.deleteSelectedTitle": "删除",
            "daisy.confirm.deleteSelectedText": "删除选定的记录",
            "daisy.confirm.deleteItemTitle": "删除",
            "daisy.confirm.deleteItemText": "删除这条记录",
            "daisy.confirm.recoverItemTitle": "恢复",
            "daisy.confirm.recoverItemText": "恢复这条记录",
            "daisy.confirm.saveOrUpdateTitle": "保存",
            "daisy.confirm.saveOrUpdateText": "确认保存",
            "daisy.confirm.cancelTitle": "取消",
            "daisy.confirm.cancelText": "确认取消",
        };
    }

    format() {
        if (arguments.length == 0 || !this.loadedStringMap[arguments[0]]) {
            return '';
        }
        let args = Array.prototype.slice.call(arguments);
        args.splice(0, 1, this.loadedStringMap[arguments[0]]);
        return format.apply(null, args);
    }
}

/**
 * 注意这个方法只能在setup函数中调用，其他时候调用会返回undefined
 * @returns StringManager的实例
 */
export function useStringManager() {
    return inject(STRING_MANAGER);
}

const StringManagerInstaller = {
    install(Vue, options) {
        var stringManager = new StringManager(options.request);
        Vue.$daisyStringManager = stringManager;
        Vue.provide(STRING_MANAGER, stringManager);

        Object.defineProperties(Vue.config.globalProperties, {
            $daisyStringManager: {
                get() {
                    return stringManager;
                }
            }
        });
    }
};

export default StringManagerInstaller;
