export default function DataContext() {
  this.record = {};
  this.initialRecord = {};
  this.updatedRecord = {};
  this.validateRule = {};
  this.option = {};
  this.intermediate = {};
  this.validateResult = {};
  this.forceMandatory = false;
}

function getFieldProxy(dataContext, fieldList, targetField) {
  (fieldList);
  let handler = {
    get(target, property) {
      if (target.getNamedValue) {
        return target.getNamedValue(fieldList, dataContext, property);
      }
    },
    set(target, property, value) {
      if (target.setNamedValue) {
        target.setNamedValue(fieldList, dataContext, property, value);
      }
      return true;
    },
  };

  return new Proxy(targetField, handler);
}

DataContext.prototype.getRuntimeContext = function (fieldList) {
  let fieldMap = {};

  fieldList.forEach(field => {
    fieldMap[field.name] = field;
    fieldMap[field.title] = field;
  });


  let handler = {
    get(target, property) {
      if (fieldMap[property]) {
        return getFieldProxy(target, fieldList, fieldMap[property]);
      }
    },
    set() {
      return false;
    },
  };

  return {
    field: new Proxy(this, handler),
  };
}

DataContext.prototype.getDirty = function (fieldList) {
  return fieldList.filter(field => this.getRuntimeContext(fieldList).field[field.name]['更新过标志']).length > 0;
}
