<template>
  <div class="selector" v-if="selectedCount > 0 || modalVisible">
    <info-circle-filled :style="{ color: '#1890FF' }" />
    <span style="margin-left: 7px">已选&nbsp;</span><span style="color: #1890FF">{{ selectedCount }}</span><span
      style="margin-right: 63px">&nbsp;项</span>
    <a-button type="link" size="small" :disabled="selectedCount == 0" @click="showSelected">
      查看
    </a-button>
    <a-button type="link" size="small" :disabled="selectedCount == 0" @click="clearSelected">
      清空
    </a-button>
    <a-modal v-model:visible="modalVisible" :bodyStyle="{ height: '587px' }" :title="modalTitle" @ok="modalHandleClose">
      <template #footer>
        <div style="text-align: center;">
          <a-button key="delete" :disabled="tableSelectedCount == 0" @click="modalHandleDelete">
            删除
          </a-button>
          <a-button key="submit" type="primary" @click="modalHandleClose">确定</a-button>
        </div>
      </template>
      <div class="selected-container">
        <div class="selector" v-if="selectedCount > 0">
          <info-circle-filled :style="{ color: '#1890FF' }" />
          <span style="margin-left: 7px">已选{{ internalEntityName }}&nbsp;</span><span style="color: #1890FF">{{
              tableSelectedCount
          }}/{{ selectedCount }}</span><span style="margin-right: 63px">&nbsp;项</span>
        </div>
        <div class="table">
          <a-table :columns="tableFieldList" :dataSource="selectedItemList" :pagination="false" :row-selection="{
            selectedRowKeys: tableSelectedKeyList,
            onChange: handleSelectionChange,
          }" bordered size="small" :scroll="{ x: 400, y: -1 }">
          </a-table>
        </div>
      </div>
    </a-modal>
  </div>
</template>
<script>
import { InfoCircleFilled } from '@ant-design/icons-vue';
import { Button, Modal, Table } from 'ant-design-vue';
import { defineComponent, reactive, unref, computed } from "vue";

export function useSelectedItemListIndicator() {
  function deselect(itemList) {
    const itemKeyList = itemList.map(item => item.key);
    context.selectedItemList = context.selectedItemList.filter(item => !itemKeyList.includes(item.key));
  }

  function select(itemList) {
    let selectedKeyItemList = context.selectedItemList.map(item => item.key);
    context.selectedItemList = context.selectedItemList.concat(itemList.filter(item => !selectedKeyItemList.includes(item.key)));
  }

  function deselectAll() {
    context.selectedItemList = [];
  }

  let context = reactive({
    propList: {
      fieldList: computed(() => context.fieldList),
      entityDisplayName: computed(() => context.entityDisplayName),
      selectedItemList: computed(() => context.selectedItemList),
      'onUpdate:selectedItemList': (value) => context.selectedItemList = value,
    },
    selectedItemList: [],
    fieldList: [],
    entityDisplayName: '',
    select,
    deselect,
    deselectAll,
  });

  return context;
}


export default defineComponent({
  name: 'ASelectedItemListIndicator',
  components: {
    InfoCircleFilled,
    'AModal': Modal,
    'AButton': Button,
    'ATable': Table,
  },
  props: {
    fieldList: {
      type: Object,
      required: true,
    },
    entityDisplayName: {
      type: String,
      required: true,
    },
    selectedItemList: {
      type: Array,
      required: true,
    },
  },
  emits: ['update:selectedItemList'],
  computed: {
    tableFieldList() {
      const columnList = [];
      for (var index = 0; index < unref(this.$props.fieldList)?.length; index++) {
        let field = unref(this.$props.fieldList)[index];
        columnList.push({
          title: field.title || field.name,
          dataIndex: field.name,
          key: field.name,
          width: field.width || 150,
          ellipsis: true,
        });
      }

      return columnList;
    },
    selectedCount() {
      return this.selectedItemList?.length;
    },
    modalTitle() {
      return '已选择' + this.internalEntityName;
    },
    internalEntityName() {
      return unref(this.$props.entityDisplayName);
    },
    tableSelectedCount() {
      return this.tableSelectedKeyList.length;
    },
  },
  methods: {
    showSelected() {
      this.modalVisible = true;
    },
    clearSelected() {
      this.$emit('update:selectedItemList', []);
    },
    handleSelectionChange(selectedKeyList) {
      this.tableSelectedKeyList = selectedKeyList;
    },
    modalHandleDelete() {
      this.$emit('update:selectedItemList', this.selectedItemList.filter(item => !this.tableSelectedKeyList.includes(item.key)));
    },
    modalHandleClose() {
      this.modalVisible = false;
    },
  },
  setup() {
    return reactive({
      modalVisible: false,
      tableSelectedKeyList: [],
    });
  },
})
</script>
