<template>
    <a-row :style="{ alignItems: 'center' }">
        <a-col :span="9" :style="{ paddingLeft: '8px', paddingRight: '8px' }">
            <a-typography
                :style="{ whiteSpace: 'nowrap', overflow: 'hidden', textOverflow: 'ellipsis', textAlign: 'right' }"
                :title="title">{{ title }}</a-typography>
        </a-col>
        <a-col :span="6">
            <a-input-number :maxlength="20" :style="{ width: '100%' }" v-model:value="valueLeft"></a-input-number>
        </a-col>
        <a-col :span="3" :style="{ textAlign: 'center' }">到</a-col>
        <a-col :span="6">
            <a-input-number :maxlength="20" :style="{ width: '100%' }" v-model:value="valueRight"></a-input-number>
        </a-col>
    </a-row>
</template>
<script>
import { defineComponent, ref } from "vue";
import { Row, Col, Typography, InputNumber } from 'ant-design-vue';
import "ant-design-vue/lib/typography/style/css";
import "ant-design-vue/lib/input-number/style/css";
import "ant-design-vue/lib/row/style/css";
import "ant-design-vue/lib/col/style/css";

export default defineComponent({
    name: "NumberConditionEditor",
    props: {
        field: {
            type: Object,
            required: true,
        }
    },
    emits: ['update:criteriaList'],
    components: {
        "ATypography": Typography,
        "AInputNumber": InputNumber,
        "ARow": Row,
        "ACol": Col,
    },
    computed: {
        title() {
            return this.$props.field.title;
        },
        criteriaList() {
            if (!this.internalValueLeft && this.internalValueLeft !== 0) {
                if (!this.internalValueRight && this.internalValueRight !== 0) {
                    return [];
                } else {
                    return [{
                        propertyName: this.$props.field.name,
                        opType: 'le',
                        value: this.internalValueRight * Math.pow(10, this.field.scale || 0),
                    }];
                }
            } else {
                if (!this.internalValueRight && this.internalValueRight !== 0) {
                    return [{
                        propertyName: this.$props.field.name,
                        opType: 'ge',
                        value: this.internalValueLeft * Math.pow(10, this.field.scale || 0),
                    }];
                } else {
                    return [{
                        propertyName: this.$props.field.name,
                        opType: 'between',
                        value: [
                            this.internalValueLeft * Math.pow(10, this.field.scale || 0),
                            this.internalValueRight * Math.pow(10, this.field.scale || 0)
                        ],
                    }];
                }
            }
        },
        valueLeft: {
            get() {
                return this.internalValueLeft;
            },
            set(value) {
                this.internalValueLeft = value;
                this.$emit("update:criteriaList", this.criteriaList);
            }
        },
        valueRight: {
            get() {
                return this.internalValueRight;
            },
            set(value) {
                this.internalValueRight = value;
                this.$emit("update:criteriaList", this.criteriaList);
            }
        },
    },
    methods: {
        reset() {
            this.internalValueLeft = '';
            this.internalValueRight = '';
            this.$emit("update:criteriaList", this.criteriaList);
        },
    },
    setup() {
        return {
            internalValueLeft: ref(),
            internalValueRight: ref(),
        };
    },
});
</script>