<template>
    <div style="display: flex; flex-direction: row;" v-if="this.rowList.length > 0">
        <div style="flex: 1 1 auto;">
            <a-row :style="{ 'padding': '15px 22px', 'align-items': 'center' }" v-for="row in this.rowList"
                :key="row.id">
                <a-col span="8" :style="{ 'padding': '0px 2px' }" v-for="field in row.fieldList" :key="field.name">
                    <component :ref="this.getComponentRef(field)" :is="this.getComponent(field)"
                        v-bind="this.getComponentPropMap(field)" v-on="this.getComponentOnMap(field)"></component>
                </a-col>
            </a-row>
        </div>
        <div style="flex: 0 0 auto; padding: 8px;">
            <a-space :size="9" :align="'end'">
                <a-button type="primary" @click="handleSearch">查询</a-button>
                <a-button @click="handleReset">重置</a-button>
            </a-space>
        </div>
    </div>
</template>
<script>
import { Button, Col, Row, Space } from 'ant-design-vue';
import "ant-design-vue/lib/button/style/css";
import "ant-design-vue/lib/col/style/css";
import "ant-design-vue/lib/row/style/css";
import "ant-design-vue/lib/space/style/css";
import { computed, defineComponent, reactive, ref } from "vue";
import DictionaryConditionEditor from "../conditionEditor/DictionaryConditionEditor.vue";
import NumberConditionEditor from "../conditionEditor/NumberConditionEditor.vue";
import StringConditionEditor from "../conditionEditor/StringConditionEditor.vue";

export function useQueryConditionPanel() {
    let context = reactive({
        propList: {
            fieldList: computed(() => {
                return context.fieldList;
            }),
            criteriaList: computed(() => context.criteriaList),
            'onUpdate:criteriaList': (value) => { context.criteriaList = value; },
        },
        fieldList: [],
        criteriaList: [],
    });

    return context;
}

export default defineComponent({
    name: "QueryConditionPanel",
    components: {
        "StringConditionEditor": StringConditionEditor,
        "NumberConditionEditor": NumberConditionEditor,
        "DictionaryConditionEditor": DictionaryConditionEditor,
        "ARow": Row,
        "ACol": Col,
        "AButton": Button,
        "ASpace": Space,
    },
    props: {
        'fieldList': {
            type: Object,
            required: true
        }
    },
    emits: ['update:criteriaList'],
    computed: {
        rowList() {
            let rowList = [];
            if (this.$props.fieldList) {
                let fieldList = this.$props.fieldList;
                let row = { id: rowList.length, fieldList: [] };

                for (let i in fieldList) {
                    let field = fieldList[i];

                    if (row.fieldList.length >= 3) {
                        rowList.push(row);
                        row = { id: rowList.length, fieldList: [] };
                    }
                    row.fieldList.push(field);
                }
                if (row.fieldList.length > 0) {
                    rowList.push(row);
                }
            }

            return rowList;
        },
        criteriaList() {
            let criteriaList = [];

            for (let i in this.fieldCriteriaListMap) {
                let fieldCriteriaList = this.fieldCriteriaListMap[i];

                criteriaList = criteriaList.concat(fieldCriteriaList);
            }

            return criteriaList;
        }
    },
    methods: {
        getComponent(field) {
            if (field.dictionary) {
                return 'DictionaryConditionEditor';
            }
            if (field.fieldType == 'string') {
                return 'StringConditionEditor';
            }
            if (field.fieldType == 'int'
                || field.fieldType == 'long'
                || field.fieldType == 'stake'
                || field.fieldType == 'kilometer'
                || field.fieldType == 'meter') {
                return 'NumberConditionEditor';
            }
            console.log('不支持的条件编辑类型', field.fieldType);
            return 'StringConditionEditor';
        },
        getComponentPropMap(field) {
            return { field };
        },
        getComponentRef(field) {
            if (!this.refMap[field.name]) {
                this.refMap[field.name] = ref();
            }

            return this.refMap[field.name];
        },
        getComponentOnMap(field) {
            if (!this.onMap[field.name]) {
                this.onMap[field.name] = {
                    'update:criteriaList': (value) => {
                        this.fieldCriteriaListMap[field.name] = value;
                    },
                };
            }

            return this.onMap[field.name];
        },
        handleSearch() {
            this.$emit("update:criteriaList", this.criteriaList);
        },
        handleReset() {
            for (let i in this.refMap) {
                let r = this.refMap[i];
                if (r.value[0].reset) {
                    r.value[0].reset();
                }
            }
            this.$emit("update:criteriaList", this.criteriaList);
        },
    },
    setup() {
        let refMap = {};
        let onMap = {};
        let fieldCriteriaListMap = reactive({});

        return {
            refMap,
            onMap,
            fieldCriteriaListMap,
        };
    }
});
</script>
