<template>
  <a-modal v-model:visible="modalVisible" :bodyStyle="{ height: '187px' }" :title="title" @ok="handleOk"
    @cancel="handleCancel">
    <template #footer>
      <div style="text-align: center;">
        <a-button key="cancel" @click="handleCancel">{{ cancelText }}</a-button>
        <a-button key="submit" type="primary" @click="handleOk">{{ okText }}
        </a-button>
      </div>
    </template>
    <div class="selected-container">{{ text }}</div>
  </a-modal>
</template>
<script>
import { useStringManager } from '@/misc/StringManager';
import { Button, Modal } from 'ant-design-vue';
import "ant-design-vue/lib/button/style/css";
import "ant-design-vue/lib/modal/style/css";
import { defineComponent, reactive, computed } from "vue";

/**
 * 注意这个函数只能在setup函数中调用，其他时候调用会出错
 */
export function useConfirm(component) {
  let stringManager = component ? component.$daisyStringManager : useStringManager();
  let resolve = null;
  let reject = null;

  function reset() {
    // 不要下面的两句，视觉上会看到对话框中的文本先消失了，然后对话框关闭了
    // reactivePropList.title = '';
    // reactivePropList.text = '';
    context.visible = false;
    reject = null;
    resolve = null;
  }

  function handleCancel() {
    if (context.visible) {
      reject();
      reset();
    }
  }

  function handleOk() {
    if (context.visible) {
      resolve();
      reset();
    }
  }

  function show(text, title) {
    if (context.visible) {
      throw new Error('提示对话框已经在显示状态了');
    }
    context.text = text;
    context.title = title;
    context.visible = true;
    return new Promise((_resolve, _reject) => {
      resolve = _resolve;
      reject = _reject;
    });
  }

  let context = reactive({
    propList: {
      visible: computed(() => context.visible),
      title: computed(() => context.title),
      text: computed(() => context.text),
      cancelText: computed(() => context.cancelText),
      okText: computed(() => context.okText),
      handleOk: computed(() => context.handleOk),
      handleCancel: computed(() => context.handleCancel),
    },
    visible: false,
    title: '',
    text: '',
    cancelText: stringManager.format('daisy.confirm.cancel'),
    okText: stringManager.format('daisy.confirm.ok'),
    show,
    handleOk,
    handleCancel,
  });

  return context;
}

export default defineComponent({
  name: 'AConfirm',
  components: {
    'AButton': Button,
    'AModal': Modal,
  },
  props: {
    visible: {
      type: [Boolean],
      required: true,
    },
    title: {
      type: String,
      required: true,
    },
    text: {
      type: String,
      required: true,
    },
    okText: {
      type: String,
      required: true,
    },
    cancelText: {
      type: String,
      required: true,
    },
    handleCancel: {
      type: Function,
      required: true,
    },
    handleOk: {
      type: Function,
      required: true,
    },
  },
  computed: {
    modalVisible: {
      get() {
        return this.$props.visible;
      },
      set(newValue) {
        this.$emit('update:visible', newValue);
      },
    },
    modalTitle() {
      return this.$props.title;
    },
    modalText() {
      return this.$props.text;
    },
    modalOkText() {
      return this.$props.okText;
    },
    modalCancelText() {
      return this.$props.cancelText;
    },
  },
});
</script>
