<template>
  <k-dynamic-component-site v-bind="dynamicComponentSite.propList"></k-dynamic-component-site>
</template>
<script>
import DynamicComponentSite, { useDynamicComponentSite } from "@/components/DynamicComponentSite.vue";
import KConfirm, { useConfirm } from "@/components/Confirm.vue";
import { defineComponent, reactive, computed } from "vue";

export function useMessageBoxProvider() {
  let dynamicComponentSite = useDynamicComponentSite();

  function confirm(text, title) {
    let confirm;
    let confirmCreatedResolve;
    let confirmCreatedPromise = new Promise(_resolve => confirmCreatedResolve = _resolve);

    let confirmDisposer = this.dynamicComponentSite.appendComponent(() => {
      if (!confirm) {
        confirm = useConfirm();
        confirmCreatedResolve();
      }

      return {
        component: KConfirm,
        propList: confirm.propList,
      };
    });

    return new Promise((resolve, reject) => {
      confirmCreatedPromise.then(() => {
        confirm.show(text, title)
          .then(result => {
            resolve(result);
          })
          .catch(result => {
            reject(result);
          })
          .finally(() => confirmDisposer());
      });
    });
  }

  let context = reactive({
    propList: {
      dynamicComponentSite: computed(() => dynamicComponentSite),
    },
    dynamicComponentSite: dynamicComponentSite,
    confirm,
  });

  return context;
}

export default defineComponent({
  name: "KMessageBoxProvider",
  components: {
    KDynamicComponentSite: DynamicComponentSite,
  },
  props: {
    dynamicComponentSite: {
      type: Object,
      required: true,
    },
  },
});
</script>
