<template>
  <a-row :style="{ alignItems: 'center' }">
    <a-col :span="3" :style="{ paddingLeft: '8px', paddingRight: '8px' }">
      <a-typography :style="{ whiteSpace: 'nowrap', overflow: 'hidden', textOverflow: 'ellipsis', textAlign: 'right' }"
        :title="title">{{ title }}</a-typography>
    </a-col>
    <a-col :span="21">
      <a-upload v-model:file-list="fileList" name="file" list-type="picture-card" :action="action" :headers="headerList"
        :disabled="$props.field.getReadonly($props.dataContext)" :max-count="maxCount" @change="handleChange" @preview="handlePreview">
        <div v-if="fileList.length < maxCount">
          <plus-outlined />
          <div style="margin-top: 8px">Upload</div>
        </div>
      </a-upload>
      <a-modal :visible="previewVisible" :title="previewTitle" :footer="null" @cancel="handleCancel">
        <img alt="example" style="width: 100%" :src="previewImage" />
      </a-modal>
      <div v-if="validateMessageList && validateMessageList.length > 0" style="height: 0; overflow: show;">
        <div v-for="validateMessage in validateMessageList" v-bind:key="validateMessage"
          style="font-size: 90%; color: red;">{{ validateMessage }}</div>
      </div>
    </a-col>
  </a-row>
</template>
<script>
import { AUTH_HEADER_PROVIDER, IMAGE_ACTION_PROVIDER, IMAGE_SHOW_PROVIDER } from "@/misc/Symbol";
import { PlusOutlined } from '@ant-design/icons-vue';
import { Col, Modal, Row, Typography, Upload } from 'ant-design-vue';
import "ant-design-vue/lib/col/style/css";
import "ant-design-vue/lib/modal/style/css";
import "ant-design-vue/lib/row/style/css";
import "ant-design-vue/lib/typography/style/css";
import "ant-design-vue/lib/upload/style/css";
import { computed, defineComponent, inject, reactive } from "vue";

function getBase64(file) {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = () => resolve(reader.result);
    reader.onerror = error => reject(error);
  });
}

export default defineComponent({
  name: "ImageFieldEditor",
  props: {
    field: {
      type: Object,
      required: true,
    },
    fieldList: {
      type: Array,
      required: true,
    },
    dataContext: {
      type: Object,
      required: true,
    },
  },
  components: {
    "ATypography": Typography,
    "AUpload": Upload,
    "AModal": Modal,
    PlusOutlined,
    "ARow": Row,
    "ACol": Col,
  },
  computed: {
    title() {
      return (this.field.mandatory ? '* ' : '') + this.field.title;
    },
    runtimeContext() {
      return this.$props.dataContext.getRuntimeContext(this.$props.fieldList);
    },
    runtimeContextOfField() {
      return this.runtimeContext.field[this.$props.field.name];
    },
    value: {
      get() {
        return this.runtimeContextOfField['值'];
      },
      set(value) {
        this.runtimeContextOfField['值'] = value;
      }
    },
    maxCount() {
      return this.$props.field.editorParameter?.maxCount || 8;
    },
    validateMessageList() {
      let validateResultList = this.runtimeContextOfField['校验结果'] || [];

      return validateResultList.map(validateResult => validateResult.message);
    },
  },
  methods: {
    handleChange(info) {
      if (info.file.status === 'done' || info.file.status === 'removed') {
        this.storageId = info.file.response.data.storageId;
        this.value = JSON.stringify(info.fileList.map(file => file.response.data));
      }
    },
    handleCancel() {
      this.previewVisible = false;
      this.previewTitle = '';
    },
    async handlePreview(file) {
      if (!file.url && !file.preview) {
        file.preview = await getBase64(file.originFileObj);
      }
      this.previewImage = file.preview || this.imageShowProvider(file.response.data);
      this.previewVisible = true;
      this.previewTitle = file.response.data.originalFilename;
    },
  },
  setup(propList) {
    const authHeaderProvider = inject(AUTH_HEADER_PROVIDER);
    const imageActionProvider = inject(IMAGE_ACTION_PROVIDER);
    const imageShowProvider = inject(IMAGE_SHOW_PROVIDER);
    let fileList = [];
    let content = propList.dataContext.getRuntimeContext(propList.fieldList).field[propList.field.name]['值'];

    if (content) {
      fileList = JSON.parse(content).map(image => {
        return {
          uid: image.filenameId,
          name: image.originalFilename,
          status: 'done',
          url: imageShowProvider(image, 'preview'),
          response: {
            data: image
          }
        };
      });
    }

    let context = reactive({
      previewVisible: false,
      previewImage: '',
      previewTitle: '',
      fileList,
      storageId: '',
      imageShowProvider,
      headerList: authHeaderProvider ? authHeaderProvider() : null,
      action: computed(() => imageActionProvider(propList.field.editorParameter.scenario, context.storageId.value)),
    });

    return context;
  },
});
</script>
