<template>
  <div class="container">
    <keep-alive exclude="DaisyCRUDDetail">
      <component :is="componentName" v-bind="componentData.propMap" v-on="componentData.handlerMap">
        <template v-for="(index, name) in $slots" v-slot:[name]>
          <slot :name="name" />
        </template>
      </component>
    </keep-alive>
  </div>
</template>

<script>
import DaisyCRUDTable from "./crud/DaisyCRUDTable.vue";
import DaisyCRUDDetail from "./crud/DaisyCRUDDetail.vue";
import { defineComponent, reactive, ref, unref } from "vue";

export default defineComponent({
  name: "DaisyCRUD",
  components: {
    DaisyCRUDTable,
    DaisyCRUDDetail,
  },
  props: {
    dataProvider: {
      type: Function,
      required: true,
    },
  },
  deactivate() {
  },
  computed: {
    componentData() {
      if (this.componentName == DaisyCRUDTable.name) {
        return {
          propMap: {
            tableRef: this.tableRef,
            title: unref(this.providedData.title),
            recordOperationList: this.providedData.recordOperationList,
            tableLoadCriteria: this.providedData.tableLoadCriteria,
            tableLoadData: this.providedData.tableLoadData,
            entityDataManager: this.providedData.entityDataManager,
            deleteRecordList: this.providedData.deleteRecordList,
          },
          handlerMap: {
            createNew: (e) => {
              this.createNew(e);
            },
            edit: (e) => {
              this.edit(e);
            },
            mount: (e) => {
              this.tableRef = e.component;
            }
          },
        };
      } else if (this.componentName == DaisyCRUDDetail.name) {
        return {
          propMap: {
            title: this.providedData.title,
            detailFormLayout: this.detailFormLayout,
            record: this.recordContainer.record,
            validatorList: unref(this.providedData.validatorList),
            fieldList: unref(this.providedData.entityDataManager.metadata)?.fieldList || [],
          },
          handlerMap: {
            saveOrUpdate: (e) => {
              this.saveOrUpdate(e);
            },
            cancel: (e) => {
              this.cancel(e);
            },
          },
        };
      }
      return null;
    },
    detailFormLayout() {
      return unref(this.providedData.detailFormLayout);
    },
  },
  methods: {
    wrapHandlerMap(handlerMap) {
      const wrappedHandlerMap = {};

      for (let key in handlerMap) {
        if (this[key]) {
          wrappedHandlerMap[key] = () => this[key].call(this);
        }
      }

      return wrappedHandlerMap;
    },
    createNew() {
      this.recordContainer.record = reactive(this.providedData.initializeRecord());
      this.componentName = DaisyCRUDDetail.name;
    },
    edit(e) {
      this.recordContainer.record = reactive(e.record);
      this.componentName = DaisyCRUDDetail.name;
    },
    saveOrUpdate(e) {
      this.providedData.saveOrUpdate.call(this, e.record).then(() => {
        this.componentName = DaisyCRUDTable.name;
        unref(this.tableRef).refresh(unref(this.tableRef).pagination);
      });
    },
    cancel() {
      this.componentName = DaisyCRUDTable.name;
    },
  },
  mounted() {
  },
  setup(props, _ref) {
    const providedData = props.dataProvider.call(_ref);
    const recordContainer = { record: ref(null) };
    const tableRef = ref({});
    const componentName = ref(DaisyCRUDTable.name);

    return reactive({
      providedData,
      recordContainer,
      componentName,
      tableRef,
    });
  },
});
</script>

<style scoped>
.container {
  position: absolute;
  left: 0px;
  right: 0px;
  top: 0px;
  bottom: 0px;
  text-align: left;
}
</style>
