export default (record, columns, args, tip) => {
  if (!columns || columns.length !== 2 || !args || args.length !== 1) {
    console.log(`validator compare columns invalid`)
  }
  const message = {
    recordMessage: '',
    columnMessage: []
  }
  try {
    const c0 = record[columns[0]];
    const c1 = record[columns[1]];
    let compareResult = null;
    if (c0 == c1) {
      compareResult = 1;
    } else if (c0 > c1) {
      compareResult = 1;
    } else if (c0 < c1) {
      compareResult = -1;
    }
    if (args[0] != compareResult) {
      message.recordMessage = tip || `${columns[0]} 和 ${columns[1]}大小关系错误`
    }
  } catch (e) {
    console.log('compare exception: ', e)
    return;
  }

  return message;
}
