<template>
    <layout-panel :itemList="layoutPanelItemList"></layout-panel>
</template>
<script>
import { defineComponent } from "vue";
import LayoutPanel from "./LayoutPanel.vue";
import StringFieldEditor from "@/components/editor/StringFieldEditor.vue";
import NumberFieldEditor from "@/components/editor/NumberFieldEditor.vue";
import DictionaryFieldEditor from "@/components/editor/DictionaryFieldEditor.vue";
import ReadonlyFieldEditor from "@/components/editor/ReadonlyFieldEditor.vue";
import DateFieldEditor from "@/components/editor/DateFieldEditor.vue";
import CompositeSerialFieldEditor from "@/components/editor/CompositeSerialFieldEditor.vue";
import EntityFieldEditor from "@/components/editor/EntityFieldEditor.vue";
import ImageFieldEditor from "../editor/ImageFieldEditor.vue";
import YearFieldEditor from "../editor/YearFieldEditor.vue";

export default defineComponent({
    name: "RecordEditPanel",
    props: {
        fieldList: {
            type: Array,
            required: true
        },
        record: {
            type: Object,
            required: true,
        }
    },
    emits: ["fieldChange"],
    components: {
        LayoutPanel,
    },
    computed: {
        layoutPanelItemList() {
            return this.fieldList.filter(field => field.showOnForm != 'disable').map((field) => {
                let span = 8;
                let componentMap = {
                    'long': NumberFieldEditor,
                    'int': NumberFieldEditor,
                    'decimal': NumberFieldEditor,
                    'stake': NumberFieldEditor,
                    'kilometer': NumberFieldEditor,
                    'meter': NumberFieldEditor,
                    'date': DateFieldEditor,
                    'year': YearFieldEditor,
                    'image': ImageFieldEditor,
                };
                let component = field.dictionary ? DictionaryFieldEditor : componentMap[field.fieldType] || StringFieldEditor;

                if (field.showOnForm == 'readonly') {
                    component = ReadonlyFieldEditor;
                }
                if (field.editor?.type == 'yearPicker') {
                    component = DateFieldEditor;
                }
                if (field.editor?.type == 'compositeSerial') {
                    component = CompositeSerialFieldEditor;
                }
                if (field.editor?.type == 'entitySelect') {
                    component = EntityFieldEditor;
                }
                if (component == ImageFieldEditor) {
                    span = 24;
                }

                return {
                    component: component,
                    propList: {
                        field: field,
                        record: this.record,
                        value: this.record[field.name],
                        onFieldChange: (e) => {
                            this.$emit("fieldChange", e);
                        }
                    },
                    span,
                }
            })
        },
    },
});

</script>