<template>
  <div class="root">
    <!-- <div class="header">
      <span>表单填写</span>
    </div> -->
    <div class="content">
      <record-edit-panel :field-list="editFieldInfoList" :record="editingRecord" @fieldChange="fieldChange" />
      <slot name="detail-content-after"/>
    </div>
    <div class="footer">
      <slot name="detail-footer">
        <a-space :size="9" align="end">
          <a-button type="primary" @click="handleSave">保存</a-button>
          <a-button @click="handleCancel">关闭</a-button>
        </a-space>
      </slot>
    </div>
  </div>
  <a-confirm v-bind="confirm.propList"></a-confirm>
</template>

<script>
// TODO: loading功能
import Confirm, { useConfirm } from "@/components/Confirm.vue";
import RecordEditPanel from '@/components/core/RecordEditPanel.vue';
import { Button, message, Space } from 'ant-design-vue';
import "ant-design-vue/lib/button/style/css";
import "ant-design-vue/lib/message/style/css";
import "ant-design-vue/lib/modal/style/css";
import { defineComponent, reactive, ref } from "vue";

export default defineComponent({
  name: "DaisyCRUDDetail",
  components: {
    'AButton': Button,
    'ASpace': Space,
    'AConfirm': Confirm,
    RecordEditPanel,
  },
  emits: ["saveOrUpdate", "cancel"],
  props: {
    title: {
      required: true,
    },
    detailFormLayout: {
      type: Object,
      required: true,
    },
    record: {
      type: Object,
      required: true,
    },
    validatorList: {
      type: Object,
      required: true
    },
    fieldList: {
      type: Array,
      required: true,
    }
  },
  activated() {
  },
  deactivated() {
    this.resetValidateMess();
  },
  computed: {
    editingRecord() {
      return reactive({ ...this.record });
    },
    dirty() {
      for (let field of this.fieldList) {
        if (this.editingRecord[field.name] != this.record[field.name]) {
          return true;
        }
      }

      return false;
    },
    hasValidateError() {
      if (this.validateResult.recordMessage && this.validateResult.recordMessage !== '') {
        return true;
      }
      for (let key in this.validateResult.fieldMessageMap) {
        return true;
      }
      return false;
    },
  },
  methods: {
    handleSave() {
      // 校验失败，标红失败的字段
      this.validateRecord(true);
      if (this.validateResult.recordMessage && this.validateResult.recordMessage !== '') {
        message.error(this.validateResult.recordMessage)
        return;
      }
      if (this.hasValidateError) {
        // TODO: 尽量不要这么做，按了按钮没有反应，用户会认为是系统问题
        message.error('页面校验不通过');
        return;
      }

      this.confirm.show(
        this.$daisyStringManager.format("daisy.confirm.saveOrUpdateText"),
        this.$daisyStringManager.format("daisy.confirm.saveOrUpdateTitle")
      ).then(() => {
        this.$emit('saveOrUpdate', {
          name: 'saveOrUpdate',
          record: this.editingRecord,
        });
      });
    },
    handleCancel() {
      if (!this.dirty) {
        this.$emit('cancel', { name: 'cancel', });
        return;
      }

      this.confirm.show(
        this.$daisyStringManager.format("daisy.confirm.cancelText"),
        this.$daisyStringManager.format("daisy.confirm.cancelTitle")
      ).then(() => {
        this.$emit('cancel', { name: 'cancel', });
      });
    },
    isFieldDirty(fieldName) {
      return this.record[fieldName] !== this.editingRecord[fieldName];
    },
    fieldChange(e) {
      this.validateResult.recordMessage = '';
      this.editFieldInfoMap[e.field.name].validateMessage = '';

      this.validateRecord();
    },
    findColumn(field) {
      for (let fc of this.fieldColumnList) {
        if (fc.field === field) {
          return fc;
        }
      }
    },
    // 校验
    validateRecord(showAllInvalidMess = false) {
      this.validateResult = this.$daisyValidatorManager.validateRecord(this.editingRecord, this.validatorList);
      for (const fieldName in this.validateResult.fieldMessageMap) {
        if (this.isFieldDirty(fieldName) || showAllInvalidMess) {
          this.editFieldInfoMap[fieldName].validateMessage = this.validateResult.fieldMessageMap[fieldName].join(', ');
        }
      }
    },
    resetValidateMess() {
      this.validateResult.recordMessage = '';
      for (let fc of this.fieldColumnList) {
        fc.vallidateInfo.validateResult = '';
      }
      this.editFieldInfoList.forEach(field => { field.validateResult = ''; });
    },
  },
  setup(propList) {
    const fieldColumnList = [];
    for (const row of propList.detailFormLayout.rowList || []) {
      for (let col of row.columnList || []) {
        if (col.field && col.type !== 'text') {
          const validateResult = '';
          col.vallidateInfo = reactive({
            validateResult
          });
          fieldColumnList.push(col);
        }
      }
    }
    const editFieldInfoList = propList.fieldList.map(field => { return { ...field }; });
    const editFieldInfoMap = {};

    editFieldInfoList.forEach(field => { editFieldInfoMap[field.name] = field; });

    return reactive({
      fieldColumnList,
      editFieldInfoList,
      editFieldInfoMap,
      validateResult: {
        recordMessage: '',
        columnMessage: []
      },
      editRecord: ref({}),
      confirm: useConfirm(),
    })
  }
});
</script>

<style scoped>
.root {
  width: 100%;
  height: 100%;
  padding: 8px;
  position: absolute;
  display: flex;
  flex-direction: column;
}

.content {
  overflow: hidden auto;
  flex: 1 1 auto;
}

.footer {
  flex: 0 0 auto;
  text-align: right;
  padding-right: 59px;
}
</style>
